import React, { Component } from 'react';

import Authentication from 'components/authentication/Authentication';
import BackendManager from 'components/common/BackendManager';
import ModalManager from 'components/common/ModalManager';
import DataPreparator from 'components/import-data/DataPreparator';
import ErrorBoundary from 'components/common/ErrorBoundary';
import ImportData from 'components/import-data/ImportData';
import ProjectChooser from 'components/app-selection/ProjectChooser';
import StateContext, { modes } from 'StateContext';
import DirectorySelect from 'components/export-data/directory-selection/DirectorySelect';
import ExportData from 'components/export-data/ExportData';
import Loading from 'components/common/Loading';
import { disableFocusOnMouseClick, reactOnTab } from 'utilities/Utilities';
import { backend } from 'utilities/BackendHelper';
import Development from 'components/common/_Development';
import DataModeling from 'components/export-data/data-modeling/DataModeling';
import UpdatePolicySelection from 'components/export-data/data-modeling/UpdatePolicySelection';

export default class Content extends Component {
  constructor(props) {
    super(props);
    this.auth = React.createRef();
    this.authToken = {};
    this.sessionRefresher = {};
  }

  componentDidMount() {
    // eslint-disable-next-line global-require, import/no-dynamic-require
    require(`style/${process.env.REACT_APP_MSTR_BACKEND}.css`);
    this.showLoadingPage();
    document.addEventListener('keydown', reactOnTab);
    disableFocusOnMouseClick('button');
  }

  componentDidUpdate() {
    disableFocusOnMouseClick('button');
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', reactOnTab);
  }

  tokenChecker = () => {
    const { api, updateStateContext } = this.context;
    this.authToken = setInterval(() => {
      if (!api.token) {
        updateStateContext({ mode: modes.AUTHENTICATION });
        clearInterval(this.sessionRefresher);
        clearInterval(this.authToken);
      }
    }, 100);
  }

  keepSessionAlive = () => {
    const { api } = this.context;
    const { timeout, sendDummyRequest } = api;
    const offset = 20;
    const interval = (timeout - offset) * 1000;
    this.sessionRefresher = setInterval(() => {
      sendDummyRequest();
    }, interval);
  }

  showLoadingPage = () => {
    const { updateStateContext } = this.context;
    const loadingFlagEnabled = document.location.search.search('loading=true') !== -1;
    if (backend === 'Jupyter' && loadingFlagEnabled) {
      updateStateContext({ mode: modes.LOADING });
    }
  }

  content = () => {
    const { t, mode } = this.context;
    switch (mode) {
      case modes.PROJECT_SELECTION:
        return <ProjectChooser />;
      case modes.IMPORT.MAIN:
        return <ImportData />;
      case modes.IMPORT.PREPARE_DATA:
        return <DataPreparator />;
      case modes.EXPORT.MAIN:
        return <ExportData />;
      case modes.EXPORT.CREATE.DATA_MODELING:
      case modes.EXPORT.UPDATE.DATA_MODELING:
        return <DataModeling />;
      case modes.EXPORT.UPDATE.POLICY_SELECTION:
        return <UpdatePolicySelection />;
      case modes.EXPORT.CREATE.DIRECTORY_SELECTION:
        return <DirectorySelect />;
      case modes.LOADING:
        return <Loading t={t} />;
      default:
        return <Authentication tokenChecker={this.tokenChecker} keepSessionAlive={this.keepSessionAlive} />;
    }
  }

  render() {
    return (
      <ErrorBoundary>
        {this.content()}
        <BackendManager ref={(backendManager) => { window.backendManager = backendManager; }} tokenChecker={this.tokenChecker} keepSessionAlive={this.keepSessionAlive} />
        <ModalManager ref={(modalManager) => { window.modalManager = modalManager; }} />
        {process.env.REACT_APP_MSTR_MODE === 'development' && <Development />}
      </ErrorBoundary>
    );
  }
}

Content.contextType = StateContext;
