/* eslint-disable camelcase */
import { supportedTypes, mapArrayToStringList } from './Utilities';

let Shiny;

if (window.Shiny) {
  Shiny = window.Shiny;
} else {
  Shiny = {
    setInputValue: (key, value) => {
      // eslint-disable-next-line no-console
      console.log('setInputValue:', key, value);
    },
  };
}

export default class RStudioHelper {
    onExportDataMode = () => {
      Shiny.setInputValue('onExportDataMode', Date.now());
    }

    onExportGatherDetails = (dataframesToGather) => {
      dataframesToGather.forEach(({ originalName, dfName }) => {
        Shiny.setInputValue('dataframeToGather', originalName);
        Shiny.setInputValue('dataframeNewName', dfName);
        Shiny.setInputValue('onExportGatherDetails', Date.now(), { priority: 'event' });
      });
    }

    sendDatasetInfo = (api, selectedDataset) => {
      const { projectId, objectId, subtype, name, parameters } = selectedDataset;
      const datasetType = supportedTypes.reportTypes.includes(subtype) ? 'report' : 'dataset';

      const body = {
        attributes: [],
        metrics: [],
        filters: [],
      };
      if (parameters.length) {
        const { attributes, metrics, filters } = parameters;
        if (attributes && attributes.length) body.attributes = attributes;
        if (metrics && metrics.length) body.metrics = metrics;
        if (filters) {
          // eslint-disable-next-line no-restricted-syntax
          for (const key of Object.keys(filters)) {
            body.filters = [...body.filters, ...filters[key]];
          }
        }
      }

      Shiny.setInputValue('projectId', projectId);
      Shiny.setInputValue('datasetId', objectId);
      Shiny.setInputValue('datasetType', datasetType);
      Shiny.setInputValue('datasetName', name);
      Shiny.setInputValue('datasetBody', JSON.stringify(body));
      Shiny.setInputValue('triggerFetchDataset', Date.now());
    };

    closeConnector = () => {
      Shiny.setInputValue('triggerClose', Date.now());
    };

    updateEnvSuggestions = (newEnvSuggestions) => {
      Shiny.setInputValue('newEnvSuggestions', JSON.stringify(newEnvSuggestions));
    };

    updateRecentProjects = (newRecentProjects) => {
      Shiny.setInputValue('newRecentProjects', JSON.stringify(newRecentProjects));
    };

    reorderColumns = (dataframe_name, columns_in_row, start_index) => {
      Shiny.setInputValue('dataframeForColReorder', dataframe_name);
      Shiny.setInputValue('columnsForReorder', JSON.stringify(columns_in_row));
      Shiny.setInputValue('reorderStartIndex', start_index);
      Shiny.setInputValue('onWrangleReorderRequest', Date.now());
    }

    sendMessageToConsole = (message) => {
      Shiny.setInputValue('msgToConsole', message);
    };

    // eslint-disable-next-line no-unused-vars
    notifyOnModeChange = (mode) => {}

    sendEnvironmentInfo = ({ username, password, loginMode, url }) => {
      Shiny.setInputValue('envUrl', `${url}`);
      Shiny.setInputValue('loginMode', loginMode);
      Shiny.setInputValue('username', username);
      Shiny.setInputValue('password', password);
    }

    exportDataframes = (selectedDataframes, folderId, saveAsName, api, certify, _description) => {
      // clear description of unwanted characters
      const description = !_description
        ? ''
        : _description.replace(/("|'|`)/gi, '_');

      Shiny.setInputValue('projectId', api.projectId);
      Shiny.setInputValue('selectedDataframes', JSON.stringify(selectedDataframes));
      Shiny.setInputValue('folderId', folderId);
      Shiny.setInputValue('saveAsName', saveAsName);
      Shiny.setInputValue('certify', certify);
      Shiny.setInputValue('description', description);
      Shiny.setInputValue('triggerExportDataframes', Date.now());
    }

    onUserTooltipOpen = () => {}

    // eslint-disable-next-line no-unused-vars
    onLogin = (token, credentials) => {
      this.sendEnvironmentInfo(credentials);
    }

    sendDataModelingSteps = (steps, selectedObjects) => {
      Shiny.setInputValue('dataModelingSteps', JSON.stringify(steps));
      Shiny.setInputValue('selectedObjects', JSON.stringify(selectedObjects));
      Shiny.setInputValue('triggerDataModelingSteps', Date.now());
    }

    updateCube = (cubeId, projectId, updatePolicies, credentials, datasetName) => {
      Shiny.setInputValue('cubeId', cubeId);
      Shiny.setInputValue('projectId', projectId);
      Shiny.setInputValue('updatePolicies', JSON.stringify(updatePolicies));
      Shiny.setInputValue('datasetName', datasetName);
      Shiny.setInputValue('triggerCubeUpdate', Date.now());
    }

    generateImportCode = (name, projectName, objectId, subtype, parameters, api, t = (text) => text) => {
      const { envUrl, username, loginMode } = api;
      const authCode = this.generateAuthenticationCode(envUrl, username, projectName, loginMode, t);
      const isCube = supportedTypes.cubeTypes.includes(subtype);
      const importCode = this.generateDatasetImportCode(name, objectId, isCube, parameters, t);
      const allCode = `${authCode}\n${importCode}`;
      Shiny.setInputValue('generatedCode', allCode);
    }

    generateAuthenticationCode = (envUrl, username, projectName, loginMode, t) => (
      `# ${t('Connect_to_the_environment')}
mstr = list()
mstr$base_url <- '${envUrl}';
mstr$username <- '${username}';

# ${t('Please_provide_password')}
mstr$password <- ''

mstr$project_name <- '${projectName}';
mstr$login_mode <- ${loginMode};
mstr$conn <- mstrio::connect_mstr(mstr$base_url, mstr$username, mstr$password, mstr$project_name, NULL, mstr$login_mode);`
    );

    generateDatasetImportCode = (name, objectId, isCube, parameters, t) => {
      const filteringCode = this.generateImportFiltersCode(parameters);
      return `
# ${t('Import_Data')}
mstr$obj_id <- '${objectId}'
mstr$obj_instance <- mstrio::${isCube ? 'Cube' : 'Report'}$new(mstr$conn, mstr$obj_id); ${filteringCode && `\n${filteringCode}`}
mstr$chunk_size <- 25000;
${name} <- mstr$obj_instance$to_dataframe(mstr$chunk_size);`;
    };

    generateImportFiltersCode = ({ attributes, metrics, filters }) => {
      const filtersCombined = filters ? Object.values(filters).flat() : [];
      const attributesList = mapArrayToStringList(attributes, ', ');
      const metricsList = mapArrayToStringList(metrics, ', ');
      const filtersList = mapArrayToStringList(filtersCombined, ', ');

      if (!attributesList && !metricsList && !filtersList) {
        return '';
      }

      return `mstr$obj_instance$apply_filters(
  attributes=list(${attributesList}),
  metrics=list(${metricsList}),
  attr_elements=list(${filtersList})
)`;
    }
}
