import React, { Component } from 'react';
import { Button, Tooltip, Row, Col, message } from 'antd';

import Footer from 'components/common/Footer';
import MainContainer from 'components/common/MainContainer';
import StateContext, { modes } from 'StateContext';
import MSTRIcon from 'components/common/icons/MSTRIcon';
import { containsSpecialCharacters, renameArrayItem, addOrRemoveItemsToArray, renameObjectProperty } from 'utilities/Utilities';
import backendHelper from 'utilities/BackendHelper';
import { displayModal } from 'components/common/Modals';
import DragAndDrop, { dndConfigConstants } from 'components/common/DragAndDrop';
import TopBar from '../TopBar';
import DataframesSelection from './DataframesSelection';
import PreviewTable from './PreviewTable';
import DetailsBox from './DetailsBox';

import './DataModeling.scss';
import '../../common/Modal.scss';

const LetsGetStartedPlaceholder = ({ t, mode }) => {
  const header = mode === modes.EXPORT.UPDATE.DATA_MODELING ? t("Let's_update_the_cube!") : t("Let's_create_the_cube!");
  return (
    <div className="lets-get-started-placeholder">
      <MSTRIcon type="lets-select-data" />
      <h2>{header}</h2>
      <p>{t("You_haven't_selected_any_dataframes_yet._To_perform_data_modeling,_select_them_from_the_side_panel.")}</p>
    </div>
  );
};

const generateObjectsArchitecture = (dataframes, selectedDataframes, selectedObjects) => {
  if (Object.keys(selectedObjects).length < 1) return {};
  const output = {};
  Object.keys(dataframes)
    .filter((dfName) => selectedDataframes.includes(dfName))
    .forEach((dfName) => {
      const dataframe = dataframes[dfName];
      if (!dataframe.types) return;
      const attributes = dataframe.types.attributes.map((attribute) => ({
        name: attribute.name[0],
        type: 'attribute',
        isSelected: selectedObjects[dfName].includes(attribute.name[0]),
      }));

      const metrics = dataframe.types.metrics.map((metric) => ({
        name: metric.name[0],
        type: 'metric',
        isSelected: selectedObjects[dfName].includes(metric.name[0]),
      }));
      output[dfName] = [...attributes, ...metrics];
    });

  return output;
};

export const stepGenerator = {
  renameDf: (oldName, newName) => ({
    type: 'RENAME_DF',
    oldName,
    newName,
  }),
  renameObj: (oldName, newName, dfName) => ({
    type: 'RENAME_OBJ',
    oldName,
    newName,
    dfName,
  }),
};

export const checkStructureConsistency = (definition, selectedDataframes, selectedObjects, t) => {
  let foundErrors = false;
  const errors = {
    tables: [],
    tableNames: [],
    columns: {},
  };
  // initial check for incorrect names in selectedObjects in pure dataframe
  selectedDataframes.forEach((dfName) => {
    errors.columns[dfName] = [];
    if (containsSpecialCharacters(dfName)) {
      foundErrors = true;
      const description = `[${dfName}]: ${t('Name_contains_forbidden_characters._Only_letters,_numbers,_and_underscores_(_)_can_be_used.')}`;
      errors.tableNames.push({ name: dfName, description });
    }
    selectedObjects[dfName].forEach((columnName) => {
      if (containsSpecialCharacters(columnName)) {
        foundErrors = true;
        const description = `[${columnName}]: ${t('Name_contains_forbidden_characters._Only_letters,_numbers,_and_some_special_characters_(%_(_)[_]_-__)_can_be_used.')}`;
        errors.columns[dfName].push({ name: columnName, description, checked: true });
      }
    });
  });
  if (!definition) return foundErrors ? errors : null;
  const cubeTables = Object.keys(definition);
  // check if there are no extra tables
  selectedDataframes.forEach((dfName) => {
    if (!cubeTables.includes(dfName)) {
      foundErrors = true;
      const description = t('Table_`{{tableName}}`_does_not_exist_in_the_updated_cube.', { tableName: dfName });
      errors.tables.push({ name: dfName, description });
    }
  });
  if (foundErrors) return errors;
  // check if there are no extra / missing columns
  selectedDataframes.forEach((dfName) => {
    const dfObjects = selectedObjects[dfName];
    const tableObjects = definition[dfName];
    // check for extra columns
    dfObjects.forEach((dfObjName) => {
      if (!tableObjects.includes(dfObjName)) {
        foundErrors = true;
        const description = t('Column_`{{columnName}}`_does_not_exist_in_`{{tableName}}`_table_of_the_updated_cube.', { columnName: dfObjName, tableName: dfName });
        errors.columns[dfName].push({ name: dfObjName, description, checked: true });
      }
    });
    // check for missing columns
    tableObjects.forEach((tableObjName) => {
      if (!dfObjects.includes(tableObjName)) {
        foundErrors = true;
        const description = t('Column_`{{columnName}}`_is_missing_from_`{{tableName}}`_table.', { columnName: tableObjName, tableName: dfName });
        errors.columns[dfName].push({ name: tableObjName, description, checked: false });
      }
    });
  });
  return foundErrors ? errors : null;
};

export const parseSelectedObjects = (rawSelectedObjects, dataframesToConsider) => {
  if (!dataframesToConsider.length) return [];
  const parsedSelectedObjects = [];
  Object.keys(rawSelectedObjects)
    .filter((dfName) => dataframesToConsider.includes(dfName))
    .forEach((dfName) => {
      const objectArray = rawSelectedObjects[dfName];
      if (objectArray.length > 0) {
        parsedSelectedObjects.push({
          dfName,
          selectedObjects: objectArray,
        });
      }
    });
  return parsedSelectedObjects;
};

export default class DataModeling extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedDataframes: [], // array of strings
      selectedObjects: {}, // object {'dataframe': array of string}
      searchText: '', // string
      viewSelected: false, // bool
      steps: [], // array of objects type StepGenerator
      previewedDf: null, // string / null
      errors: null,
    };
  }

  componentDidMount() {
    const { dataframes } = this.context;
    const selectedObjects = {};
    Object.keys(dataframes).forEach((dataframe) => {
      selectedObjects[dataframe] = [];
    });
    this.setState({ selectedObjects });
  }

  componentDidUpdate(prevProps, { previewedDf }) {
    if (previewedDf !== this.state.previewedDf) {
      const elementsToScrollIntoView = [...document.getElementsByClassName('previewed')];
      elementsToScrollIntoView.length && elementsToScrollIntoView.forEach((object) => {
        object.scrollIntoView({
          behaviour: 'smooth',
          block: 'center',
          inline: 'center',
        });
      });
    }
  }

  handleBack = () => {
    this.context.updateStateContext({ mode: modes.EXPORT.MAIN, reloadDataSource: false });
  }

  handleNext = () => {
    const { mode, updateStateContext } = this.context;
    const { selectedDataframes, selectedObjects, steps } = this.state;
    const parsedSelectedObjects = parseSelectedObjects(selectedObjects, selectedDataframes);
    if (mode === modes.EXPORT.UPDATE.DATA_MODELING) {
      const isStructureValid = this.validateStructureConsistencyUpdateCube();
      if (!isStructureValid) return;
    } else {
      const isStructureValid = this.validateStructureConsistencyExportCube();
      if (!isStructureValid) return;
    }
    backendHelper.sendDataModelingSteps(steps, parsedSelectedObjects);
    const dataframesWithSelectedColumns = this.getDataframesWithSelectedColumns();
    updateStateContext({ dataModelingInProgress: true, selectedDataframes: dataframesWithSelectedColumns });
  }

  getDataframesWithSelectedColumns = () => {
    const { dataframes } = this.context;
    const { selectedDataframes, selectedObjects } = this.state;
    const dfsWithAllObjects = generateObjectsArchitecture(dataframes, selectedDataframes, selectedObjects);
    return Object.keys(dfsWithAllObjects).map((name) => {
      const attributes = dfsWithAllObjects[name].filter((object) => object.isSelected && object.type === 'attribute').map((object) => object.name);
      const metrics = dfsWithAllObjects[name].filter((object) => object.isSelected && object.type === 'metric').map((object) => object.name);
      return {
        name,
        attributes,
        metrics,
      };
    });
  }

  addModelingStep = (newStep) => {
    const steps = [...this.state.steps];
    steps.push(newStep);
    this.setState({ steps });
  }

  onSearch = (event) => {
    this.setState({ searchText: event.target.value });
  }

  onViewSelectedChange = () => {
    this.setState((state) => ({ viewSelected: !state.viewSelected }));
  }

  onClearAll = () => {
    this.setState({ selectedDataframes: [], previewedDf: null });
  }

  validateStructureConsistencyUpdateCube = () => {
    const { t, selectedCubeToUpdate: { definition } } = this.context;
    const { selectedDataframes, selectedObjects } = this.state;
    const structureConsistencyErrors = checkStructureConsistency(definition, selectedDataframes, selectedObjects, t);
    if (structureConsistencyErrors) {
      const title = t('The_data_structure_modeled_by_you_does_not_correspond_to_the_structure_of_the_updated_cube.');
      const content = structureConsistencyErrors.tables.length
        ? structureConsistencyErrors.tables.map(({ description }) => <p>{description}</p>)
        : Object.values(structureConsistencyErrors.columns).map((tableObjects) => (
          tableObjects.map(({ description }) => <p>{description}</p>)
        ));
      displayModal('error', title, content, () => { this.setState({ errors: structureConsistencyErrors }); });
      return false;
    }
    return true;
  }

  validateStructureConsistencyExportCube = () => {
    const { t } = this.context;
    const { selectedDataframes, selectedObjects } = this.state;
    const structureConsistencyErrors = checkStructureConsistency(null, selectedDataframes, selectedObjects, t);
    if (structureConsistencyErrors) {
      const title = t('Some_elements_names_contain_forbidden_characters.');
      const content = [
        ...Object.values(structureConsistencyErrors.columns).map((tableObjects) => (
          tableObjects.map(({ description }) => <p>{description}</p>)
        )),
        ...structureConsistencyErrors.tableNames.map(({ description }) => <p>{description}</p>),
      ];
      displayModal('error', title, content, () => { this.setState({ errors: structureConsistencyErrors }); });
      return false;
    }
    return true;
  }

  nameValidator = (_name) => {
    const { t, mode } = this.context;
    const minLength = 1;
    const name = _name.trim();

    if (name.length < minLength) {
      const errorMessage = t('New_name_is_too_short._Please_try_again.');
      message.error(errorMessage);
      return false;
    }

    if (mode === modes.EXPORT.UPDATE.DATA_MODELING) return true;

    const containsSpecialChars = containsSpecialCharacters(name);
    if (containsSpecialChars) {
      const errorMessage = t('Name_contains_forbidden_characters._Only_letters,_numbers,_and_some_special_characters_(%_(_)[_]_-__)_can_be_used.');
      message.error(errorMessage);
      return false;
    }

    message.destroy();
    return true;
  }

  dfNameValidator = (newName) => {
    const { t, dataframes } = this.context;
    const isValid = this.nameValidator(newName);
    const isUnique = !Object.keys(dataframes).includes(newName);
    if (!isUnique) {
      const errorMessage = t('Table_name_`{{tableName}}`_already_exists._Please_try_again.', { tableName: newName });
      message.error(errorMessage);
    }
    return isValid && isUnique;
  }

  dfNameSetter = (oldName, newName) => {
    const { updateStateContext } = this.context;
    const selectedDataframes = renameArrayItem(oldName, newName, this.state.selectedDataframes);
    const previewedDf = this.state.previewedDf === oldName ? newName : this.state.previewedDf;
    const dataframes = renameObjectProperty(oldName, newName, this.context.dataframes);
    const selectedObjects = renameObjectProperty(oldName, newName, this.state.selectedObjects);
    const step = stepGenerator.renameDf(oldName, newName);
    this.addModelingStep(step);
    this.setState({ selectedDataframes, selectedObjects, previewedDf });
    updateStateContext({ dataframes });
  }

  onDfSelect = (dfName, index = -1) => {
    const { dataframes } = this.context;
    let selectedDataframes;
    let previewedDf;
    if (this.state.selectedDataframes.includes(dfName)) {
      selectedDataframes = this.state.selectedDataframes.filter((name) => name !== dfName);
      previewedDf = this.state.previewedDf === dfName ? null : this.state.previewedDf;
    } else {
      previewedDf = dfName;
      selectedDataframes = [...this.state.selectedDataframes];
      selectedDataframes.splice(index < 0 ? selectedDataframes.length : index, 0, dfName);
      if (!dataframes[dfName].types) {
        const { originalName } = dataframes[dfName];
        backendHelper.onExportGatherDetails([{ originalName, dfName }]);
        this.context.updateStateContext({ loadingDfContent: true });
      }
    }
    this.setState({ selectedDataframes, previewedDf });
  }

  onPreviewDf = (dfName) => {
    this.setState({ previewedDf: dfName });
  }

  onSelectAllDataframes = () => {
    const { dataframes } = this.context;
    let selectedDataframes;
    let previewedDf;

    if (this.state.selectedDataframes.length) {
      selectedDataframes = [];
      previewedDf = null;
    } else {
      selectedDataframes = [...Object.keys(this.context.dataframes)];
      const dataframesToGather = [];
      selectedDataframes.forEach((dfName) => {
        if (!dataframes[dfName].types) {
          const { originalName } = dataframes[dfName];
          dataframesToGather.push({ originalName, dfName });
        }
      });
      [previewedDf] = selectedDataframes;
      backendHelper.onExportGatherDetails(dataframesToGather);
    }
    this.setState({ selectedDataframes, previewedDf });
  }

  selectObjectsHandler = (dataframeName, objectsList) => {
    const selectedObjects = { ...this.state.selectedObjects };
    selectedObjects[dataframeName] = addOrRemoveItemsToArray(objectsList, this.state.selectedObjects[dataframeName]);
    this.setState({ selectedObjects });
  }

  moveObjectsHandler = (dataframeName, objectsList, target, _index = -1) => {
    const dataframe = { ...this.context.dataframes[dataframeName] };
    const index = _index < 0 ? dataframe.types[target].length : _index;
    const source = target === 'attributes' ? 'metrics' : 'attributes';
    objectsList.forEach((obj) => {
      const toMoveIndex = dataframe.types[source].findIndex((item) => item.name[0] === obj);
      dataframe.types[target].splice(index, 0, dataframe.types[source][toMoveIndex]);
      dataframe.types[source].splice(toMoveIndex, 1);
    });
    const dataframes = {
      ...this.context.dataframes,
      [dataframeName]: dataframe,
    };
    this.context.updateStateContext({ dataframes });
  }

  objectNameValidator = (dataframeName, objName) => {
    const { dataframes, t } = this.context;
    const isUnique = !Object.keys(dataframes[dataframeName].content[0]).includes(objName);
    if (!isUnique) {
      const errorMessage = t('Column_name_`{{colName}}`_already_exists._Please_try_again.', { colName: objName });
      message.error(errorMessage);
      return false;
    }
    return this.nameValidator(objName);
  };

  setObjectNameHandler = (dataframeName, oldName, newName) => {
    const dataframe = { ...this.context.dataframes[dataframeName] };
    dataframe.content = dataframe.content.map((dataRow) => renameObjectProperty(oldName, newName, dataRow));
    dataframe.types.tables[0].columnHeaders.find(({ name }) => name[0] === oldName).name[0] = newName;
    const metricToRename = dataframe.types.metrics.find(({ name }) => name[0] === oldName);
    if (metricToRename) {
      metricToRename.name[0] = newName;
    }
    const attributeToRename = dataframe.types.attributes.find(({ name }) => name[0] === oldName);
    if (attributeToRename) {
      attributeToRename.name[0] = newName;
    }
    const dataframes = {
      ...this.context.dataframes,
      [dataframeName]: dataframe,
    };
    const step = stepGenerator.renameObj(oldName, newName, dataframeName);
    this.addModelingStep(step);
    this.context.updateStateContext({ dataframes });
  }

  onDfDropHandler = (dataframes, index) => {
    dataframes.forEach((dataframe) => {
      !this.state.selectedDataframes.includes(dataframe) && this.onDfSelect(dataframe, index);
      this.onPreviewDf(dataframe);
    });
  }

  render() {
    const { t, mode, dataframes, dataModelingInProgress } = this.context;
    const { searchText, viewSelected, selectedDataframes, selectedObjects, previewedDf, errors } = this.state;
    const nextButtonDisabled = !parseSelectedObjects(selectedObjects, selectedDataframes).length;
    const selectAllButtonText = selectedDataframes.length > 0 ? 'Clear_All' : 'Select_All';
    const objects = generateObjectsArchitecture(dataframes, selectedDataframes, selectedObjects);
    const dfNames = Object.keys(dataframes).map((dfName) => ({
      initial: dataframes[dfName].originalName,
      current: dfName,
    }));

    const detailsBoxes = selectedDataframes.map((dfName) => {
      const isLoading = !Object.prototype.hasOwnProperty.call(dataframes[dfName], 'content');
      const isPreviewed = dfName === previewedDf;
      const isPreviewedByError = errors && errors.tables.find(({ name }) => name === dfName);
      const nameIsIncorrect = errors && errors.tableNames.find(({ name }) => name === dfName);
      const warningItems = errors ? (errors.columns[dfName] || []) : [];
      return (
        <DetailsBox
          isLoading={isLoading}
          isPreviewed={isPreviewed}
          isPreviewedByError={isPreviewedByError}
          nameIsIncorrect={nameIsIncorrect}
          warningItems={warningItems}
          dfName={dfName}
          objects={objects[dfName]}
          searchText={searchText.toLowerCase()}
          hideUnselected={viewSelected}
          onPreviewDf={this.onPreviewDf}
          dfNameValidator={this.dfNameValidator}
          dfNameSetter={this.dfNameSetter}
          objNameValidator={(name) => this.objectNameValidator(dfName, name)}
          objNameSetter={this.setObjectNameHandler}
          onObjsSelect={this.selectObjectsHandler}
          onObjsMove={this.moveObjectsHandler}
          t={t}
          key={`${dfName}_${isPreviewed}`}
        />
      );
    });

    const warningItemsForPreviewTable = errors ? (errors.columns[previewedDf] || []) : [];
    const previewTableSetObjectNameHandler = (prevName, newName) => {
      objects[previewedDf].find(({ name }) => name === prevName).isSelected && this.selectObjectsHandler(previewedDf, [prevName, newName]);
      this.setObjectNameHandler(previewedDf, prevName, newName);
    };

    return (
      <MainContainer>
        <Row className="full-height">
          <TopBar mode={mode} onSearch={this.onSearch} t={t} searchText={searchText} viewSelected={viewSelected} onViewSelectedChange={this.onViewSelectedChange} />
          <Row className="browser-content import data-modeling-container">
            <DragAndDrop
              containersGroupName="data-frames-container"
              itemsGroupName="data-frames-item"
              onDrop={(dfs, target, index) => { this.onDfDropHandler(dfs, index); }}
              onDropSwapOnly={() => {}} // to allow index selection
            >
              <Col className="full-height left-panel dataframes-selection-panel">
                <DataframesSelection
                  dfNames={dfNames}
                  selectedDfs={selectedDataframes}
                  previewedDf={previewedDf}
                  onPreviewDf={this.onPreviewDf}
                  onSelect={this.onDfSelect}
                  t={t}
                />
                <button className="clear-all-button" type="button" onClick={this.onSelectAllDataframes}>{t(selectAllButtonText)}</button>
              </Col>
              <Col className="full-height right-panel" tabIndex={-1}>
                <div
                  className="display-boxes-container"
                  data-dnd-container="data-frames-container"
                  data-dnd-target="dataframes-list"
                  data-dnd-config={dndConfigConstants.verticalItems}
                >
                  { !selectedDataframes.length > 0
                    && <LetsGetStartedPlaceholder t={t} mode={mode} />}
                  { selectedDataframes.length > 0
                    && detailsBoxes}
                </div>
                <PreviewTable
                  dfName={previewedDf}
                  dataframe={dataframes[previewedDf]}
                  selectedObjects={selectedObjects[previewedDf]}
                  warningItems={warningItemsForPreviewTable}
                  dfNameValidator={this.dfNameValidator}
                  dfNameSetter={this.dfNameSetter}
                  objNameValidator={(name) => this.objectNameValidator(previewedDf, name)}
                  objNameSetter={previewTableSetObjectNameHandler}
                  t={t}
                />
              </Col>
            </DragAndDrop>
          </Row>
        </Row>
        <Footer
          left={<Button id="back" onClick={this.handleBack}>{t('Back')}</Button>}
          right={(
            <div>
              <Tooltip placement="topRight" mouseEnterDelay={1} overlayClassName="info-tooltip">
                <Button id="create" type="primary" onClick={this.handleNext} disabled={nextButtonDisabled} loading={dataModelingInProgress}>{t('Next')}</Button>
              </Tooltip>
            </div>
          )}
        />
      </MainContainer>
    );
  }
}

DataModeling.contextType = StateContext;
