import React from 'react';

import './Modal.scss';
import { Trans } from 'react-i18next';
import { Modal } from 'antd';
import { LoadingText } from '@mstr/mstr-react-library';

export const displayModal = (type, title, content, onOk = () => {}) => {
  Modal[type]({
    title,
    content,
    width: 520,
    height: 150,
    onOk,
  });
};

const CancelInfo = (t) => (
  <p className="cancel-info">
    <Trans i18nKey="Press_<1>stop_button</1>_to_cancel" t={t}>
      Press <span className="stop-icon">stop button</span> to cancel
    </Trans>
  </p>
);

export const LoadingModal = ({ content, t = (text) => text, overlayClassName = '' }) => (
  <Modal visible closable={false} zIndex={20} footer={null} wrapClassName={`loading-modal ${overlayClassName}`}>
    {content}
    {CancelInfo(t)}
    <LoadingText text="" />
  </Modal>
);

export default null;
