import React, { useState } from 'react';
import PropTypes from 'prop-types';

import './TextToInput.scss';

const MAX_LENGTH = 245;

export default function TextToInput({ value, validator, setter, disabled = false, onToggle = () => {} }) {
  const [isInputShown, setIsInputShown] = useState(false);
  const [inputValue, setInputValue] = React.useState(value);

  const toggle = () => {
    !disabled && setIsInputShown((state) => !state);
    !disabled && !isInputShown && onToggle(value);
    !isInputShown && setInputValue(value);
  };

  const onInputChange = (event) => {
    setInputValue(event.target.value.substring(0, MAX_LENGTH));
  };

  const onBlur = (event) => {
    if (value === inputValue) {
      toggle();
      return;
    }

    const isValid = validator(inputValue);
    if (isValid) {
      setter(value, inputValue);
      toggle();
    } else {
      event.target.focus();
    }
  };

  const onInputKeyDown = (event) => event.key === 'Enter' && onBlur(event);
  const onButtonKeyDown = (event) => event.key === 'Enter' && toggle();

  if (isInputShown) {
    // eslint-disable-next-line jsx-a11y/no-autofocus
    return <input key={inputValue} className="text-to-input-field" type="text" value={inputValue} onChange={onInputChange} onBlur={onBlur} onKeyDown={onInputKeyDown} maxLength={MAX_LENGTH} autoFocus />;
  }

  return <button key={inputValue} className="text-to-input-button" type="button" onDoubleClick={toggle} onKeyDown={onButtonKeyDown}>{value}</button>;
}

TextToInput.propTypes = {
  value: PropTypes.string.isRequired,
  validator: PropTypes.func,
  setter: PropTypes.func,
  disabled: PropTypes.bool,
  onToggle: PropTypes.func,
};
