import React from 'react';
import PropTypes from 'prop-types';
import { Empty } from 'antd';
import Checkbox from 'components/common/Checkbox';
import MSTRIcon from 'components/common/icons/MSTRIcon';
import Popover from 'components/common/Popover';
import { compareByName } from 'utilities/Utilities';
import TextToInput from './TextToInput';

export default function CategoryBox({ title, dfName, icon, objects = [], warningItems = [], nameValidator = () => {}, objNameSetter = () => {}, onObjsSelect = () => {}, t = (text) => text }) {
  const [objectsToDisplayAlertsFor, setObjectsToDisplayAlertsFor] = React.useState([]);

  React.useEffect(() => {
    setObjectsToDisplayAlertsFor(
      warningItems.map(({ name }) => name),
    );
  }, [warningItems]);

  const hideAlertForObject = (object) => {
    setObjectsToDisplayAlertsFor(
      objectsToDisplayAlertsFor.filter((item) => item !== object),
    );
  };

  const listOfObjects = objects.sort(compareByName).map((obj) => {
    const alert = warningItems.find((item) => item.name === obj.name && objectsToDisplayAlertsFor.includes(item.name) && item.checked === obj.isSelected);
    return (
      <li
        key={`${obj.name}_${obj.isSelected}`}
        title={obj.name}
        data-dnd-item={`${dfName}_dnd_item`}
        data-dnd-value={obj.name}
      >
        <label
          htmlFor={`${obj.name}-list-element-${dfName}`}
        >
          <Checkbox
            checked={obj.isSelected}
            id={`${obj.name}-list-element-${dfName}`}
            onChange={() => { onObjsSelect(dfName, [obj.name]); }}
          />
          {icon}
          <TextToInput
            value={obj.name}
            validator={nameValidator}
            setter={(prevName, nextName) => {
              obj.isSelected && onObjsSelect(dfName, [prevName, nextName]);
              objNameSetter(dfName, prevName, nextName);
            }}
            onToggle={hideAlertForObject}
          />
        </label>
        {
          alert && (
            <Popover content={alert.description} placement="top" trigger="hover">
              <div>
                <MSTRIcon type="red-info" />
              </div>
            </Popover>
          )
        }
      </li>
    );
  });

  const selectAllSelected = objects.length > 0 ? !objects.some(({ isSelected }) => !isSelected) : false;

  const selectAllHandler = () => {
    onObjsSelect(dfName, objects.filter(({ isSelected }) => isSelected === selectAllSelected).map(({ name }) => name));
  };

  return (
    <div
      className="categorybox-holder"
      data-dnd-container={`${dfName}_dnd_container`}
      data-dnd-target={`${dfName}_${title.toLowerCase()}`}
    >
      <div
        className="selector-title"
      >
        <Checkbox
          checked={selectAllSelected}
          id={`${title}-${dfName}-select-all`}
          onChange={() => { selectAllHandler(); }}
        />
        {t(title)}
      </div>
      <div
        className="categorybox-content"
      >
        <ul className="main-list">
          {objects.length < 1 && <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description={t('No_Data')} />}
          {objects.length > 0 && listOfObjects}
        </ul>
      </div>
    </div>
  );
}

CategoryBox.propTypes = {
  title: PropTypes.string.isRequired,
  dfName: PropTypes.string.isRequired,
  icon: PropTypes.node.isRequired,
  objects: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    type: PropTypes.string,
    isSelected: PropTypes.bool,
  })),
  warningItems: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    description: PropTypes.string,
    checked: PropTypes.bool,
  })),
  nameValidator: PropTypes.func,
  objNameSetter: PropTypes.func,
  onObjsSelect: PropTypes.func,
  t: PropTypes.func,
};
