/* eslint-disable no-param-reassign */
/* eslint-disable no-restricted-syntax */
/* eslint-disable class-methods-use-this */
/* eslint-disable consistent-return */
import { message } from 'antd';
import request from 'superagent';
import axios from 'axios';

import { applicationType } from './BackendHelper';
import { supportedTypes } from './Utilities';

const USE_PROXY = false;
const TOKEN_HEADER = 'x-mstr-authtoken';
const PROJECT_ID_HEADER = 'X-MSTR-ProjectID';
const MIN_ISERVER_VERSION = '11.1.0400';
const ATTEMPTS_BEFORE_CORS_MSG = 2;
message.config({ maxCount: 1 });

const ERROR_TYPES = {
  CANT_GET_ISERVER_VERSION: 'cant-get-iserver-version',
  INSUFFICIENT_ISERVER_VERSION: 'insufficient-iserver-version',
  HTTP_ERROR_WITH_STATUS_CODE: 'http-error-with-status-code',
  SERVER_OFFLINE: 'server-offline',
  CORS: 'cors',
  SETTING_UP_THE_REQUEST: 'setting-up-the-request',
  WRONG_CREDENTIALS: 'wrong-credentials',
  NO_LICENSE: 'no-license',
};

export const defineType = (subtype) => {
  const def = 'cube';
  const subtypeNum = Number(subtype);
  if (supportedTypes.reportTypes.includes(subtypeNum)) return 'report';
  if (supportedTypes.cubeTypes.includes(subtypeNum)) return 'cube';
  return def;
};

export default class MSTRFetch {
  constructor() {
    this.supportedTypes = [3, 8, 768, 769, 779, 776];
    this.userInfo = {
      fullName: 'MSTR User',
      initials: 'MU',
    };

    this.t = (text) => text;
    this.token = null;
    this.projectId = '';
    this.connectionAttempts = 0;
  }

  setCredentials = ({ url, username, password, loginMode }) => {
    this.envUrl = url.slice(0, -1);
    this.username = username;
    this.password = password;
    this.loginMode = loginMode;
  }

  logout = () => {
    this.token = null;
  }

  getHeaders = () => ({
    [TOKEN_HEADER]: this.token,
    [PROJECT_ID_HEADER]: this.projectId,
    'Content-Type': 'application/json',
    Accept: 'application/json',
  });

  getData(endpoint) {
    const url = `${this.envUrl}/${endpoint}`;
    return axios.get(url, {
      headers: this.getHeaders(),
      withCredentials: true,
    });
  }

  headData(endpoint) {
    const url = `${this.envUrl}/${endpoint}`;
    return axios.head(url, {
      headers: this.getHeaders(),
      withCredentials: true,
    });
  }

  postData(endpoint, data) {
    const url = `${this.envUrl}/${endpoint}`;
    return axios.post(url, data, {
      headers: this.getHeaders(),
      withCredentials: true,
    });
  }

  handleRejectedRequest = (error, mode) => {
    if (error.response) {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      this.handleHTTPError(error.response.status, mode);
    } else if (error.request) {
      // The request was made but no response was received
      // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
      // http.ClientRequest in node.js
      if (this.connectionAttempts < ATTEMPTS_BEFORE_CORS_MSG) {
        this.displayErrorMessage(ERROR_TYPES.SERVER_OFFLINE);
      }
      else {
        this.displayErrorMessage(ERROR_TYPES.CORS);
      }
      this.connectionAttempts += 1;
    } else {
      // Something happened in setting up the request that triggered an Error
      this.displayErrorMessage(ERROR_TYPES.SETTING_UP_THE_REQUEST);
    }
  }

  handleHTTPError = (errorCode, mode) => {
    if (mode === 'check-version' && errorCode === 404) {
      this.displayErrorMessage(ERROR_TYPES.SERVER_OFFLINE);
    }
    else if (mode === 'auth' && errorCode === 401) {
      this.displayErrorMessage(ERROR_TYPES.WRONG_CREDENTIALS);
    }
    else if (mode === 'check-license' && errorCode === 403) {
      this.displayErrorMessage(ERROR_TYPES.NO_LICENSE);
    }
    else {
      this.displayErrorMessage(ERROR_TYPES.HTTP_ERROR_WITH_STATUS_CODE, { errorCode });
    }
  }

  displayErrorMessage = (errorType, extraParams = {}) => {
    let msg;
    const { t } = this;
    const { errorCode, iServerVersion } = extraParams;
    switch (errorType) {
      case ERROR_TYPES.HTTP_ERROR_WITH_STATUS_CODE:
        msg = t('Could_not_process_your_request._Server_responded_with_{{errorCode}}_error_code.', { errorCode });
        break;
      case ERROR_TYPES.CANT_GET_ISERVER_VERSION:
        msg = t('Could_not_get_the_IServer_version.');
        break;
      case ERROR_TYPES.INSUFFICIENT_ISERVER_VERSION:
        msg = t(t('Your_MicroStrategy_version_{{yourVersion}}_is_not_supported_by_MSTRIO._Please_upgrade_to_version_{{minIServerVersion}}_or_higher.', { yourVersion: iServerVersion, minIServerVersion: MIN_ISERVER_VERSION }));
        break;
      case ERROR_TYPES.SERVER_OFFLINE:
        msg = t('Could_not_process_your_request._The_environment_URL_you_provided_may_be_incorrect_or_the_server_is_down.');
        break;
      case ERROR_TYPES.CORS:
        msg = t('Could_not_process_your_request._If_you_are_sure_that_the_environment_information_is_correct,_contact_your_administrator_to_enable_CORS_on_your_server.');
        break;
      case ERROR_TYPES.WRONG_CREDENTIALS:
        msg = t('Your_username,_password_or_authentication_mode_is_incorrect.');
        break;
      case ERROR_TYPES.NO_LICENSE:
        msg = t('Insufficient_privileges_to_use_this_service._Contact_your_administrator.');
        break;
      case ERROR_TYPES.SETTING_UP_THE_REQUEST:
        msg = t('Could_not_process_your_request._Error_during_setting_up_the_request.');
        break;
      default:
        break;
    }
    msg && message.error(this.t(msg), 6);
  }

  handleHTTPErrorOld = (error, mode = 'request') => {
    const { status } = error;
    let msg;
    if (status === 401 || status === 503) {
      let unauthMsg;
      if (mode === 'request') {
        unauthMsg = "The_user's_session_has_expired,_please_reauthenticate.";
        this.logout();
      }
      else if (mode === 'auth') {
        unauthMsg = 'Your_username,_password_or_authentication_mode_is_incorrect.';
      }
      msg = this.t(unauthMsg);
    }
    else {
      msg = this.t('Could_not_process_your_request._Server_responded_with_{{errorCode}}_error_code.', { errorCode: status });
    }
    if (msg !== undefined) message.warning(msg, 4);
    return Promise.reject(error);
  }

  getDataOld(endpoint, _projectId = '') {
    const projectId = this.projectId || _projectId;
    const url = `${this.envUrl}/${endpoint}`;
    return request
      .get(url)
      .set('Accept', 'application/json')
      .set('Content-Type', 'application/json')
      .set(TOKEN_HEADER, this.token)
      .set(PROJECT_ID_HEADER, projectId)
      .withCredentials()
      .then((res) => res.body)
      .catch((err) => this.handleHTTPErrorOld(err));
  }

  postDataOld(endpoint, data = {}, _projectId = '') {
    const projectId = this.project || _projectId;
    const url = `${this.envUrl}/${endpoint}`;
    return request.post(url)
      .set('Accept', 'application/json')
      .set('Content-Type', 'application/json')
      .set(TOKEN_HEADER, this.token)
      .set(PROJECT_ID_HEADER, projectId)
      .send(JSON.stringify(data))
      .withCredentials()
      .then((res) => res.body)
      .catch((err) => this.handleHTTPErrorOld(err));
  }

  getToken = () => {
    const data = {
      username: this.username,
      password: this.password,
      loginMode: this.loginMode,
      applicationType,
    };
    return this.postData('auth/login', data)
      .then(async (response) => {
        const token = response.headers[TOKEN_HEADER];
        this.token = token;
        return this.hasPrivileges();
      })
      .catch((error) => this.handleRejectedRequest(error, 'auth'));
  }

  checkIServerVersion = () => this.getData('status')
    .then((result) => {
      const { iServerVersion } = result.data;
      if (!iServerVersion) {
        return this.displayErrorMessage(ERROR_TYPES.CANT_GET_ISERVER_VERSION);
      }
      if (iServerVersion < MIN_ISERVER_VERSION) {
        return this.displayErrorMessage(ERROR_TYPES.INSUFFICIENT_ISERVER_VERSION, { iServerVersion });
      }
      return true;
    })
    .catch((error) => this.handleRejectedRequest(error, 'check-version'))

  hasPrivileges = () => this.getData('projects')
    .then(() => true)
    .catch((error) => {
      this.token = null;
      return this.handleRejectedRequest(error, 'check-license');
    })

  getSessionInfo = async () => this.getDataOld('sessions')
    .then((res) => {
      const { initials, fullName, timeout, id } = res;
      this.userInfo = {
        fullName,
        initials,
        id,
      };
      this.timeout = timeout;
    })

  getCubeInfo(cubeId) {
    return this.getDataOld(`cubes/${cubeId}`);
  }

  getCube(cubeId, limit = -1) {
    return this.postDataOld(`cubes/${cubeId}/instances?offset=0&limit=${limit}`);
  }

  getCubeAttributeElements(cubeId, instanceId, attributeId) {
    return this.getDataOld(`cubes/${cubeId}/instance/${instanceId}/attributes/${attributeId}/elements`);
  }

  getReportInfo(reportId) {
    return this.getDataOld(`reports/${reportId}`);
  }

  getReport(reportId) {
    return this.postDataOld(`reports/${reportId}/instances?offset=0&limit=-1`);
  }

  getFolderInfo(folderId) {
    return this.getDataOld(`objects/${folderId}?type=8`);
  }

  getReportAttributeElements(reportId, attributeId) {
    return this.getReportDetails(reportId, [attributeId], [], []).then((report) => {
      if (!report) {
        return;
      }
      const attributes = [];
      if (report.result && report.result.data.root && report.result.data.root.children) {
        report.result.data.root.children.forEach((item) => {
          attributes.push({ name: item.element.name, id: item.element.id });
        });
      }
      return attributes;
    });
  }

  getProjects = async () => this.getDataOld('projects')
    .then((res) => res)
    .catch(() => [])

  getRootFolders(projectId = this.projectId) {
    this.projectId = projectId;
    return this.getDataOld('folders?offset=0&limit=-1').then((folders) => {
      if (!folders) return;
      this.createUniqueFolderKey(folders, projectId);
      const res = this.sortAndFilterArray(folders.filter((folder) => !folder.hidden));
      return res;
    });
  }

  getMyPersonalObjects(project = this.project) {
    return this.getDataOld('folders/myPersonalObjects').then((folders) => {
      this.createUniqueFolderKey(folders, project);
      return this.sortAndFilterArray(folders.filter((folder) => !folder.hidden));
    });
  }

  getFolderContent(folderId, project = this.project) {
    return this.getDataOld(`folders/${folderId}?offset=0&limit=-1`)
      .then((folderContent) => {
        if (!folderContent) return;
        this.createUniqueFolderKey(folderContent, folderId + project);
        return this.sortAndFilterArray(folderContent);
      });
  }

  searchDataset(_name) {
    const name = _name.replace(/#/g, '');
    const encodedName = USE_PROXY ? name : encodeURIComponent(name);
    return this.getDataOld(`searches/results?name=${encodedName}&pattern=4&type=3&getAncestors=true&limit=-1&certifiedStatus=ALL`)
      .then((response) => {
        let { result } = response;
        const { totalItems } = response;
        result = this.sortAndFilterArray(result);
        return { result, totalItems };
      });
  }

  // eslint-disable-next-line class-methods-use-this
  createUniqueFolderKey(array, prefix) {
    array.forEach((item) => {
      // eslint-disable-next-line no-param-reassign
      item.key = item.id + prefix;
    });
  }

  sortAndFilterArray(array) {
    if (!array || array.length === 0) {
      return [];
    }
    const filteredArray = array.filter((element) => this.supportedTypes.includes(element.type)
                || this.supportedTypes.includes(element.subtype)
                || !element.subtype);

    return filteredArray.sort((a, b) => {
      const nameA = a.name.toUpperCase();
      const nameB = b.name.toUpperCase();
      if (nameA < nameB) {
        return -1;
      }
      if (nameA > nameB) {
        return 1;
      }
      return 0;
    });
  }

  flattenResult(result, preview) {
    try {
      if (result && result.definition && result.data) {
        let attributes = [];
        let metrics = [];
        let row = [];
        const rawData = [];
        const attributesobj = [];
        let temp;
        const rowobj = {};
        result.definition.attributes.forEach((att) => {
          if (att.forms.length > 1) {
            att.forms.forEach((form) => {
              attributesobj.push(`${att.name} ${form.name}`);
            });
          } else {
            attributesobj.push(att.name);
          }
        });
        attributes = result.definition.attributes.map((att) => att.name);
        metrics = result.definition.metrics.map((mm) => mm.name);
        const attNum = attributes.length;
        const visit = (node) => {
          if (node.element) {
            rowobj[node.depth] = node.element.formValues;
          }

          if (node.metrics || !node.children) {
            metrics.forEach((metric, i) => {
              const value = preview ? node.metrics[metric].fv : node.metrics[metric].rv;
              rowobj[attNum + i] = value;
            });
            temp = {};
            row = {};
            attributes.forEach((att, i) => {
              temp[att] = rowobj[i];
            });
            metrics.forEach((metric, i) => {
              temp[metric] = rowobj[attNum + i];
            });

            // Resolve multi-form attributes
            for (const k in temp) {
              if (typeof (temp[k]) === 'object') {
                if (Object.keys(temp[k]).length > 1) {
                  for (const form in temp[k]) {
                    if (form) {
                      row[`${k} ${form}`] = temp[k][form];
                    }
                  }
                } else {
                  for (const form in temp[k]) {
                    if (form) {
                      row[k] = temp[k][form];
                    }
                  }
                }
              } else {
                row[k] = temp[k];
              }
            }
            rawData.push(row);
          }
        };

        const search = (root) => {
          if (root) {
            visit(root);
            root.visited = true;
            if (!root.metrics && root.children) {
              root.children.forEach((node) => {
                if (!node.visited) {
                  search(node);
                }
              });
            }
          }
        };

        search(result.data.root);
        return {
          attributes: attributesobj,
          metrics,
          data: rawData,
        };
      }
      return null;
    } catch (error) {
      return null;
    }
  }

  isSessionAlive = async () => this.getDataOld('sessions')
    .then(() => true)
    .catch(() => false)

  sendDummyRequest = () => {
    this.getDataOld('sessions');
  }

  // from folder-browser-rest.js
  getProjectsForBrowsing = async (envUrl, authToken) => request
    .get(`${envUrl}/projects`)
    .set('x-mstr-authtoken', authToken)
    .withCredentials()
    .then((res) => {
      const projectList = {};
      res.body && res.body.forEach((project) => {
        projectList[project.id] = project.name;
      });
      return projectList;
    })
    .catch((err) => this.handleHTTPErrorOld(err))

  getNumberOfElements = async (envUrl, authToken, typeId) => request
    .get(`${envUrl}/searches/results?pattern=4&type=${typeId}&getAncestors=false&limit=1&certifiedStatus=ALL`)
    .set('x-mstr-authtoken', authToken)
    .withCredentials()
    .then((res) => res.body.totalItems)
    .catch((err) => this.handleHTTPErrorOld(err))

  getLimitedSmartFolderContent = async (envUrl, authToken, typeId, offset, limit) => request
    .get(`${envUrl}/searches/results?pattern=4&type=${typeId}&getAncestors=false&limit=${limit}&offset=${offset}&certifiedStatus=ALL`)
    .set('x-mstr-authtoken', authToken)
    .withCredentials()
    .then((res) => res.body.result)
    .catch((err) => this.handleHTTPErrorOld(err))

  getObjectParams = async (envUrl, authToken, projectId, objectId) => request
    .get(`${envUrl}/objects/${objectId}?type=3`)
    .set('x-mstr-authtoken', authToken)
    .set('x-mstr-projectid', projectId)
    .withCredentials()
    .then((res) => res.body)
    .catch((err) => this.handleHTTPErrorOld(err))

  getCubeSize = async (envUrl, authToken, projectId, objectId) => request
    .get(`${envUrl}/cubes?id=${objectId}`)
    .set('x-mstr-authtoken', authToken)
    .set('x-mstr-projectid', projectId)
    .withCredentials()
    .then((res) => res.body.cubesInfos[0].size)
    .catch((err) => this.handleHTTPErrorOld(err))


  isPrompted = async (objectId, projectId) => {
    const url = `${this.envUrl}/reports/${objectId}/prompts`;
    return request(url)
      .set('Accept', 'application/json')
      .set('Content-Type', 'application/json')
      .set(TOKEN_HEADER, this.token)
      .set(PROJECT_ID_HEADER, projectId)
      .withCredentials()
      .then((res) => {
        if (res.body.length > 0) return true;
        return false;
      })
      .catch((err) => this.handleHTTPErrorOld(err))
      .catch((err) => err.status);
  };

  createFolder = async (name, parent, projectId) => {
    const data = { name, parent };
    const url = `${this.envUrl}/folders`;

    return request.post(url)
      .set('Accept', 'application/json')
      .set('Content-Type', 'application/json')
      .set(TOKEN_HEADER, this.token)
      .set(PROJECT_ID_HEADER, projectId)
      .send(JSON.stringify(data))
      .withCredentials()
      .then(() => ({ success: true }))
      .catch((err) => this.handleHTTPErrorOld(err, 'folder-creation'))
      .catch((err) => ({ success: false, message: err.response.body.message }));
  }

  getCubeDefinition = async (cubeId, projectId) => {
    const url = `datasets/${cubeId}?fields=columns&fields=tables`;
    return this.getDataOld(url, projectId)
      .then((res) => res)
      .catch(() => false);
  }

  getCubeStatus = async (cubeId, projectId) => {
    this.projectId = projectId;
    const url = `cubes/${cubeId}`;
    return this.headData(url)
      .then((res) => res.headers['x-mstr-cubestatus'])
      .catch((err) => this.handleRejectedRequest(err));
  }

  getReportDefinition = async (reportId, projectId) => {
    this.projectId = projectId;
    const url = `v2/reports/${reportId}`;
    return this.getData(url)
      .then((res) => res.data.definition)
      .catch((err) => this.handleRejectedRequest(err));
  }
}
