import React from 'react';
import { Modal } from 'antd';

import { backend } from 'utilities/BackendHelper';
import StateContext from 'StateContext';

export default class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false };
  }

  componentDidCatch(error, info) {
    this.setState({ hasError: true });
    console.error(error, info);
  }

    onRefresh = () => {
      window.location.reload();
    }

    render() {
      const { t } = this.context;
      const { hasError } = this.state;
      const { children } = this.props;
      if (hasError) {
        Modal.error({
          title: t('MicroStrategy_for_{{programName}}', { programName: backend }),
          content: t('An_error_occured._Please_try_again.'),
          onOk: this.onRefresh,
          okText: t('Restart'),
        });
        return <div style={{ height: '200px', width: '100%' }} />;
      }
      return children;
    }
}

ErrorBoundary.contextType = StateContext;
