
import React, { Component } from 'react';
import { Button, Tooltip } from 'antd';

import Footer from 'components/common/Footer';
import Select from 'components/common/Select';
import MainContainer from 'components/common/MainContainer';
import StateContext, { modes } from 'StateContext';
import backendHelper, { backend } from 'utilities/BackendHelper';
import MSTRIcon from 'components/common/icons/MSTRIcon';
import Popover from 'components/common/Popover';
import StepIndicator from '../StepIndicator';

import './UpdatePolicySelection.scss';

export const POLICIES = ['Add', 'Update', 'Upsert', 'Replace'];

export default class UpdatePolicySelection extends Component {
  constructor(props) {
    super(props);

    this.state = {
      updatePolicies: {},
    };
  }

  componentDidMount() {
    const { selectedDataframes } = this.context;
    const updatePolicies = {};
    selectedDataframes.forEach(({ name }) => {
      updatePolicies[name] = undefined;
    });
    this.setState({ updatePolicies });
  }

  handleBack = () => {
    this.context.updateStateContext({ mode: modes.EXPORT.MAIN, reloadDataSource: false });
  }

  handleNext = () => {
    const { updateStateContext, api: { envUrl, username, password, loginMode } } = this.context;
    const { selectedCubeToUpdate: { objectId, projectId, name } } = this.context;
    const parsedUpdatePolicies = this.parseUpdatePolicies(this.state.updatePolicies);
    backendHelper.updateCube(objectId, projectId, parsedUpdatePolicies, { envUrl, username, password, loginMode }, name);
    updateStateContext({ importOrExportInProgress: true });
    backend === 'Jupyter' && updateStateContext({ mode: modes.EXPORT.MAIN });
  }

  parseUpdatePolicies = (rawUpdatePolicies) => {
    const parsedUpdatePolicies = [];
    Object.keys(rawUpdatePolicies).forEach((tableName) => {
      const updatePolicy = rawUpdatePolicies[tableName];
      parsedUpdatePolicies.push({
        tableName,
        updatePolicy,
      });
    });
    return parsedUpdatePolicies;
  }

  onChange = (dfName, newOption) => {
    this.setState((state) => ({
      updatePolicies: {
        ...state.updatePolicies,
        [dfName]: newOption,
      },
    }));
  }

  render() {
    const { t, mode, selectedDataframes, importOrExportInProgress } = this.context;
    const { updatePolicies } = this.state;
    const nextButtonDisabled = Object.values(updatePolicies).includes(undefined);
    const nextButtonTooltip = nextButtonDisabled ? t('This_button_is_disabled_because_you_didn’t_assign_Update_Policies_to_all_dataframes.') : null;
    const options = POLICIES.map((policy) => ({
      value: policy.toLowerCase(),
      displayValue: t(policy).toUpperCase(),
    }));

    const tooltipContent = (
      <div className="update-policy-selection-tooltip">
        <h3>{t('Update_Policies')}</h3>
        <p>{`${t('Add').toUpperCase()} - ${t('Inserts_new,_unique_rows')}`}</p>
        <p>{`${t('Update').toUpperCase()} - ${t('Updates_data_in_existing_rows_and_columns_when_they_match')}`}</p>
        <p>{`${t('Upsert').toUpperCase()} - ${t('Adds_new,_unique_rows_and_updates_existing_ones')}`}</p>
        <p>{`${t('Replace').toUpperCase()} - ${t('Replaces_the_existing_table_with_a_new_one')}`}</p>
      </div>
    );

    return (
      <MainContainer>
        <div className="update-policy-selection-content">
          <div className="search-bar">
            <div className="left">
              <StepIndicator mode={mode} t={t} />
            </div>
            <div className="right">
              <Popover content={tooltipContent} trigger="hover" placement="bottomRight">
                <div>
                  <MSTRIcon type="blue-info" />
                </div>
              </Popover>
            </div>
          </div>
          <div className="update-policy-selection-table-container">
            <table className="update-policy-selection-table">
              <thead>
                <tr>
                  <th className="dataframe-name">
                    {t('Dataframe')}
                  </th>
                  <th className="policy-selection">
                    {t('Update_Policy')}
                  </th>
                </tr>
              </thead>
              <tbody>
                {selectedDataframes.map(({ name: dfName }) => (
                  <tr key={`select-${dfName}`}>
                    <td className="dataframe-name">
                      <Tooltip placement="bottomLeft" title={dfName} mouseEnterDelay={1} overlayClassName="no-arrow">
                        {dfName}
                      </Tooltip>
                    </td>
                    <td className="policy-selection">
                      <Select options={options} placeholder={t('Select_Update_Policy')} onChange={(newOption) => this.onChange(dfName, newOption)} />
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
        <Footer
          left={<Button id="back" onClick={this.handleBack}>{t('Back')}</Button>}
          right={(
            <div>
              <Tooltip placement="topRight" mouseEnterDelay={1} overlayClassName="info-tooltip" title={nextButtonTooltip}>
                <Button id="create" type="primary" onClick={this.handleNext} disabled={nextButtonDisabled} loading={importOrExportInProgress}>{t('Publish')}</Button>
              </Tooltip>
            </div>
          )}
        />
      </MainContainer>
    );
  }
}

UpdatePolicySelection.contextType = StateContext;
