import React from 'react';
import PropTypes from 'prop-types';
import { Popover } from 'antd';

import './Popover.scss';

export default function MstrPopover({ content, children, overlayClassName = '', placement = 'bottom', visible = false }) {
  if (visible) {
    return (
      <Popover content={content} overlayClassName={`mstr-popover ${overlayClassName}`} placement={placement} visible>
        {children}
      </Popover>
    );
  }

  return (
    <Popover content={content} overlayClassName={`mstr-popover ${overlayClassName}`} placement={placement}>
      {children}
    </Popover>
  );
}

MstrPopover.propTypes = {
  children: PropTypes.node,
  content: PropTypes.node,
  overlayClassName: PropTypes.string,
  placement: PropTypes.string,
  visible: PropTypes.bool,
};
