import React, { Component } from 'react';
import { Row, Col, Button, message, Modal, Tooltip } from 'antd';
import SearchBar from 'components/common/SearchBar';
import Checkbox from 'components/common/Checkbox';
import MainContainer from 'components/common/MainContainer';
import Footer from 'components/common/Footer';
import StateContext, { modes } from 'StateContext';
import backendHelper, { backend } from 'utilities/BackendHelper';
import { disableFocusOnMouseClick, sleep } from 'utilities/Utilities';
import './DirectorySelect.scss';
import MSTRIcon from 'components/common/icons/MSTRIcon';
import { displayModal } from 'components/common/Modals';
import { Trans } from 'react-i18next';
import FolderContent from './FolderContent';
import FolderTree from './FolderTree';

const REGEX_DATASET_NAME = /^\w+[\w\s]*$/;
const REGEX_FOLDER_NAME = REGEX_DATASET_NAME;
const DESC_MAX_LEN = 250;

export default class DirectorySelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDirectory: {
        type: '',
        id: '',
        projectId: '',
      },
      saveAsName: '',
      saveAsNameValid: false,
      treeSelectedKeys: [],
      treeExpandedKeys: [],
      treeComponentKey: Date.now(),
      newFolderName: '',
      newFolderNameValid: false,
      newFolderModalVisible: false,
      searchText: '',
      descriptionLength: 0,
      certifyDataset: false,
    };
    this.descTextareaRef = React.createRef();
  }

  componentDidMount() {
    disableFocusOnMouseClick('.folder-tree-container, bu');
  }

  componentDidUpdate() {
    disableFocusOnMouseClick('button');
  }


    handleBack = () => {
      const { updateStateContext } = this.context;
      updateStateContext({ mode: modes.EXPORT.MAIN });
    }

    onSave = () => {
      const { saveAsName: cubeName } = this.state;
      const { t } = this.context;
      const itemsInCurrentFolder = window.folderContent.getItems();
      const itemWithSameName = itemsInCurrentFolder.filter(({ name }) => name === cubeName)[0];
      if (itemWithSameName === undefined) {
        this.startExport();
      }
      else if (itemWithSameName.typeName === 'cube') {
        const modalTitle = (
          <Trans t={t} i18nKey="<0>{{cubeName}}</0>_already_exists._Do_you_want_to_replace_it?">
            <strong>{{ cubeName }}</strong> already exists. Do you want to replace it?
          </Trans>
        );
        const modalBody = t('Replacing_it_will_overwrite_its_current_contents.');
        displayModal('confirm', modalTitle, modalBody, () => this.startExport());
      }
      else if (itemWithSameName.typeName === 'report') {
        const modalTitle = t('A_report_with_the_same_name_already_exists_in_this_folder.');
        const modalContent = t('Please_choose_a_different_name.');
        displayModal('warning', modalTitle, modalContent);
      }
    }

    startExport = () => {
      const { selectedDirectory: { id }, certifyDataset } = this.state;
      const { selectedDataframes, api, updateStateContext } = this.context;
      const { saveAsName } = this.state;
      const description = this.descTextareaRef.current.value;
      backendHelper.exportDataframes(selectedDataframes, id, saveAsName, api, certifyDataset, description);
      updateStateContext({ importOrExportInProgress: true });
      backend === 'Jupyter' && updateStateContext({ mode: modes.EXPORT.MAIN });
    }

    onSaveAsChange = (e) => {
      const saveAsName = e.target.value;
      const saveAsNameValid = REGEX_DATASET_NAME.test(saveAsName);
      this.setState({ saveAsNameValid, saveAsName });
    }

    // eslint-disable-next-line no-unused-vars
    onObjectChosen = (objectId, projectId, subtype, name, projectName) => {
      this.setState({ saveAsName: name }, () => this.onSaveAsChange({ target: { value: name } }));
    }

    onContentDoubleClick = (name, id, _projectId, type, treeKey) => {
      const projectId = type === 'project' ? id : _projectId;
      const treeSelectedKeys = [treeKey];
      this.onTreeExpand(null, { expanded: true, node: { props: { eventKey: treeSelectedKeys[0] } } });
      this.setState({ selectedDirectory: { type, id, projectId }, treeSelectedKeys });
    }


    onTreeSelect = (selectedKeys, e) => {
      const { treeExpandedKeys } = this.state;
      const { selectedProjects } = this.context;

      const { eventKey } = e.node.props;
      let treeExpandedKeysNew = [...treeExpandedKeys];
      if (!treeExpandedKeys.includes(eventKey)) {
        treeExpandedKeysNew = [...treeExpandedKeysNew, eventKey];
        this.setState({ treeExpandedKeys: treeExpandedKeysNew });
      }

      const treeSelectedKeys = [e.node.props.eventKey];
      const pos = e.node.props.pos.split('-')[1];
      const projectId = selectedProjects[pos];
      const { id, type } = e.node.props.dataRef;
      const typeName = type ? 'folder' : 'project';
      this.onTreeExpand(null, { expanded: true, node: { props: { eventKey } } });
      this.setState({ selectedDirectory: { type: typeName, id, projectId }, treeSelectedKeys });
    }

    onTreeExpand = (keys, event) => {
      const { treeExpandedKeys } = this.state;
      const { expanded } = event;
      const { eventKey } = event.node.props;
      let treeExpandedKeysNew = [...treeExpandedKeys];
      if (expanded) {
        treeExpandedKeysNew = [...treeExpandedKeysNew, eventKey];
      }
      else {
        treeExpandedKeysNew = treeExpandedKeysNew.filter((e) => e !== eventKey);
      }
      this.setState({ treeExpandedKeys: treeExpandedKeysNew });
    }

    onNewFolder = async () => {
      this.setState({ newFolderModalVisible: true });
      await sleep(1);
      disableFocusOnMouseClick('button');
    }

    onModalOk = () => {
      const { newFolderName } = this.state;
      this.createNewFolder(newFolderName);
    }

    onModalCancel = () => {
      this.clearAndHideModal();
    }

    onNewFolderNameInputChange = (e) => {
      const newFolderName = e.target.value;
      const newFolderNameValid = REGEX_FOLDER_NAME.test(newFolderName);
      this.setState({ newFolderName, newFolderNameValid }, () => disableFocusOnMouseClick('.ant-modal-footer button'));
    }

    clearAndHideModal = () => {
      this.setState({
        newFolderModalVisible: false,
        newFolderName: '',
      });
    }

    createNewFolder = async (name) => {
      const { selectedDirectory: { id, projectId } } = this.state;
      const { t, api } = this.context;
      const newFolderJob = await api.createFolder(name, id, projectId);
      if (newFolderJob.success) {
        this.clearAndHideModal();
        const msg = t('Folder_created_successfully.');
        message.success(msg);
        document.querySelector('.footer').focus();
        window.folderContent.loadFolder(true);
        this.setState({ treeComponentKey: Date.now() });
      }
      else {
        let msg;
        if (newFolderJob.message === 'Cannot_create_object_because_the_name_is_in_use._Please_use_a_different_name.') {
          msg = t('Cannot_create_folder_because_the_name_is_in_use._Please_use_a_different_name.');
        }
        else {
          msg = t("Cannot_create_folder._It_may_be_that_you_don't_have_permissions_to_create_folders_in_this_directory_or_the_name_you_selected_is_already_in_use.");
        }
        message.error(msg, 6);
        document.querySelector('.right-panel').focus();
      }
    }

    onSearch = (e) => {
      const { value } = e.target;
      this.setState({ searchText: value });
    }

    onDescriptionChange = () => {
      this.setState({ descriptionLength: this.descTextareaRef.current.textLength });
    }

    onCertifyChange = () => {
      this.setState((prevState) => ({ certifyDataset: !prevState.certifyDataset }));
    }

    render() {
      const { t, importOrExportInProgress, api, selectedProjects, mode, envName } = this.context;
      const { selectedDirectory, saveAsName, saveAsNameValid, newFolderModalVisible, newFolderName, newFolderNameValid, searchText, treeSelectedKeys, treeExpandedKeys, treeComponentKey, descriptionLength, certifyDataset } = this.state;

      const isRStudio = backend === 'RStudio';
      const disallowedDirectory = selectedDirectory.id === selectedDirectory.projectId;
      const disableSave = selectedDirectory.id === '' || selectedDirectory.type !== 'folder' || !saveAsNameValid || importOrExportInProgress || disallowedDirectory;
      const saveButtonTooltipTitle = disableSave
        ? importOrExportInProgress
          ? isRStudio
            ? null
            : t('This_button_is_disabled_because_another_dataset_is_being_currently_fetched_or_exported.')
          : disallowedDirectory
            ? t("This_button_is_disabled_because_you_can't_save_objects_to_this_folder.")
            : t("This_button_is_currently_disabled_because_you_didn't_choose_the_output_directory_or_the_name_you_specified_is_invalid.")
        : null;
      const saveAsInputClassName = (!saveAsName) ? 'valid' : saveAsNameValid ? 'valid' : 'invalid';
      const folderNameInputClassName = (!newFolderName) ? 'valid' : newFolderNameValid ? 'valid' : 'invalid';
      const saveAsInputTooltipVisible = (saveAsName.length > 0 && !saveAsNameValid);
      const folderNameInputTooltipVisible = newFolderName && !newFolderNameValid;
      const newFolderModalOkButtonDisabled = !(newFolderName && newFolderNameValid);

      return (
        <MainContainer>
          <Row className="full-height directory-selection">
            <SearchBar
              onSearch={this.onSearch}
              searchText={searchText}
              mode={mode}
              t={t}
              stepIndicatorVisible
            />
            <Row className="browser-content directory-selection-content">
              <Col className="full-height left-panel" tabIndex="0">
                <div className="envir-name-container">
                  <MSTRIcon type="environment" />
                  <span className="envir-name">
                    {envName}
                  </span>
                </div>
                <FolderTree
                  api={api}
                  onTreeSelect={this.onTreeSelect}
                  hideDatasets
                  selectedProjects={selectedProjects}
                  selectedKeys={treeSelectedKeys}
                  onTreeExpand={this.onTreeExpand}
                  expandedKeys={treeExpandedKeys}
                  key={treeComponentKey}
                />
              </Col>
              <Col className="full-height right-panel">
                <div className="name-desc-form">
                  <div className="form-row">
                    <div className="label-container">
                      <label htmlFor="saveas">{t('Save_as')}</label>
                    </div>
                    <div className="field-container">
                      <Tooltip visible={saveAsInputTooltipVisible} placement="bottom" title={t('Dataset_name_can_contain_only_latin_letters,_numbers,_spaces_and_underscores.')}>
                        <input type="text" name="saveas" id="saveas" value={saveAsName} onChange={this.onSaveAsChange} className={`field ${saveAsInputClassName}`} maxLength="100" />
                      </Tooltip>
                    </div>
                  </div>
                  <div className="form-row">
                    <div className="label-container">
                      <label htmlFor="desc">{t('Description')}</label>
                    </div>
                    <div className="field-container">
                      <textarea id="desc" maxLength={DESC_MAX_LEN} className="field" ref={this.descTextareaRef} onChange={this.onDescriptionChange} />
                      <div className="textarea-length">{`${descriptionLength}/${DESC_MAX_LEN}`}</div>
                    </div>
                  </div>
                </div>
                <FolderContent
                  mstrApi={api}
                  onDoubleClick={this.onContentDoubleClick}
                  onObjectChosen={this.onObjectChosen}
                  selectedProjects={selectedProjects}
                  selectedDirectory={selectedDirectory}
                  selectableCubes
                  searchText={searchText}
                  ref={(folderContent) => { window.folderContent = folderContent; }}
                />
              </Col>
            </Row>
          </Row>
          <Footer
            left={(
              <div>
                <Button id="back" onClick={this.handleBack}>{t('Back')}</Button>
                <Button id="newFolder" onClick={this.onNewFolder}>{t('New_folder')}</Button>
                <Modal
                  title={t('Create_new_folder')}
                  visible={newFolderModalVisible}
                  onOk={this.onModalOk}
                  onCancel={this.onModalCancel}
                  okButtonProps={{ disabled: newFolderModalOkButtonDisabled }}
                >
                  <p>{t('Specify_new_folder_name')}</p>
                  <Tooltip visible={folderNameInputTooltipVisible} placement="bottom" title={t('Folder_name_can_contain_only_latin_letters,_numbers,_spaces_and_underscores.')}>
                    <input type="text" name="new-folder-name-input" id="new-folder-name-input" value={newFolderName} onChange={this.onNewFolderNameInputChange} className={folderNameInputClassName} maxLength="100" autoComplete="off" />
                  </Tooltip>
                </Modal>
              </div>
            )}
            right={(
              <div className="footer-right">
                <div className="certify-container">
                  <Checkbox id="certify" checked={certifyDataset} onChange={this.onCertifyChange} />
                  <label htmlFor="certify">
                    <Tooltip placement="top" title="A certification stamp indicates that the object has been certified and user can trust its content." mouseEnterDelay={1} overlayClassName="info-tooltip">
                      <span><MSTRIcon type="certified" /></span>
                    </Tooltip>
                    {t('Certify')}
                  </label>
                </div>
                <Tooltip placement="topRight" title={saveButtonTooltipTitle} mouseEnterDelay={1} overlayClassName={disableSave ? 'info-tooltip' : 'hidden'}>
                  <Button id="save" onClick={this.onSave} type="primary" disabled={disableSave}>{t('Save')}</Button>
                </Tooltip>
              </div>
            )}
          />
        </MainContainer>
      );
    }
}

DirectorySelect.contextType = StateContext;
