import React from 'react';
import PropTypes from 'prop-types';

import MSTRIcon from 'components/common/icons/MSTRIcon';
import Popover from 'components/common/Popover';
import { compareByName } from 'utilities/Utilities';
import TextToInput from './TextToInput';

import './PreviewTable.scss';

export default function PreviewTable({ dataframe = null, dfName = '', selectedObjects = [], warningItems = [], objNameValidator = () => {}, objNameSetter = () => {}, dfNameValidator = () => {}, dfNameSetter = () => {}, t = (text) => text }) {
  let tableHeaders;
  let tableRows;

  const isEmpty = !dataframe || !dataframe.content;

  if (!isEmpty) {
    const attributes = dataframe.types.attributes.filter(({ name }) => selectedObjects.includes(name[0])).sort(compareByName);
    const metrics = dataframe.types.metrics.filter(({ name }) => selectedObjects.includes(name[0])).sort(compareByName);

    if (attributes.length + metrics.length > 0) {
      tableHeaders = [
        ...attributes.map(({ name }) => {
          const alert = warningItems.find((item) => item.name === name[0] && item.checked);
          return (
            <th key={name[0]}>
              <span>
                <span>
                  <MSTRIcon type="attribute" />
                  <TextToInput value={name[0]} validator={objNameValidator} setter={objNameSetter} />
                </span>
                {
                  alert && (
                    <Popover content={alert.description} placement="top" trigger="hover">
                      <div>
                        <MSTRIcon type="red-info" />
                      </div>
                    </Popover>
                  )
                }
              </span>
            </th>
          );
        }),
        ...metrics.map(({ name }) => {
          const alert = warningItems.find((item) => item.name === name[0] && item.checked);
          return (
            <th key={name[0]}>
              <span>
                <span>
                  <MSTRIcon type="metric" />
                  <TextToInput value={name[0]} validator={objNameValidator} setter={objNameSetter} />
                </span>
                {
                  alert && (
                    <Popover content={alert.description} placement="top" trigger="hover">
                      <div>
                        <MSTRIcon type="red-info" />
                      </div>
                    </Popover>
                  )
                }
              </span>
            </th>
          );
        }),
      ];

      tableRows = dataframe.content.map((dataRow, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <tr key={`${index}_row`}>
          {[
            ...attributes.map(({ name }) => <td key={`${dataRow[name[0]]}_${name[0]}`}>{dataRow[name[0]]}</td>),
            ...metrics.map(({ name }) => <td key={`${dataRow[name[0]]}_${name[0]}`}>{dataRow[name[0]]}</td>),
          ]}
        </tr>
      ));
    }
  }

  const infoText = selectedObjects.length ? t('Table_preview_displays_up_to_10_rows_of_data') : t('Select_columns_to_display_data_preview');

  return (
    <div className="preview-table-container">
      <div className="description-holder">
        <span>
          {!isEmpty && <TextToInput value={dfName} validator={dfNameValidator} setter={dfNameSetter} />}
        </span>
        <span className="preview-note">
          <span><MSTRIcon type="blue-info" /></span>
          <span>{infoText}</span>
        </span>
      </div>
      {
        !isEmpty && (
        <div className="table-holder">
          <table>
            <thead>
              <tr>{tableHeaders}</tr>
            </thead>
            <tbody>{tableRows}</tbody>
          </table>
        </div>
        )
      }
    </div>
  );
}

PreviewTable.propTypes = {
  dataframe: PropTypes.shape({
    originalName: PropTypes.string,
    content: PropTypes.array,
    types: PropTypes.shape({
      metrics: PropTypes.array,
      attributes: PropTypes.array,
    }),
  }),
  dfName: PropTypes.string,
  selectedObjects: PropTypes.arrayOf(PropTypes.string),
  warningItems: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    description: PropTypes.string,
    checked: PropTypes.bool,
  })),
  objNameValidator: PropTypes.func,
  objNameSetter: PropTypes.func,
  dfNameValidator: PropTypes.func,
  dfNameSetter: PropTypes.func,
  t: PropTypes.func,
};
