import React, { Component } from 'react';
import { Popover, Tooltip } from 'antd';
import StateContext, { modes } from 'StateContext';
import backendHelper, { backend, helpUrl } from 'utilities/BackendHelper';
import { sleep, disableFocusOnMouseClick } from 'utilities/Utilities';

import i18n from 'i18n';
import './UserTooltip.scss';

const CONTACT_US_MAIL = 'info@microstrategy.com';
const lb = '%0D%0A'; // line break for mail

export default class UserTooltip extends Component {
  constructor() {
    super();

    this.state = {
      visible: false,
    };
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.outsideClickListener);
  }

  handleManageApplications = () => {
    const { updateStateContext } = this.context;
    updateStateContext({ mode: modes.PROJECT_SELECTION });
  }

  handleLogout = () => {
    const { updateStateContext, api } = this.context;
    api.logout();
    updateStateContext({ reloadDataSource: true });
  }

  onOpenTootip = async () => {
    backendHelper.onUserTooltipOpen();
    this.setState({ visible: true });
    await sleep(1);
    disableFocusOnMouseClick('.user-tooltip-overlay a, .user-tooltip-overlay button');
    document.addEventListener('click', this.outsideClickListener);
    this.manageKeysInTooltip();
  }

  outsideClickListener = (event) => {
    const userTooltip = document.querySelector('.user-tooltip-overlay');
    if (!userTooltip.contains(event.target)) {
      this.setState({ visible: false });
      document.removeEventListener('click', this.outsideClickListener);
    }
  };

  manageKeysInTooltip = () => {
    const userTooltip = document.querySelector('.user-tooltip-overlay');
    const linksContainer = userTooltip.querySelector('.links');
    linksContainer.focus();
    const links = [...linksContainer.querySelectorAll('a, button:not([disabled])')];
    const firstLink = links[0];
    const lastLink = links[links.length - 1];

    const trapTabKey = (e) => {
      const { keyCode, shiftKey } = e;
      if (keyCode === 9) {
        if (shiftKey && document.activeElement === firstLink) {
          e.preventDefault();
          lastLink.focus();
        }
        else if (!shiftKey && document.activeElement === lastLink) {
          e.preventDefault();
          firstLink.focus();
        }
      }

      if (keyCode === 27) {
        this.setState({ visible: false });
        document.querySelector('.logo').focus();
        document.removeEventListener('click', this.outsideClickListener);
      }
    };

    linksContainer.addEventListener('keydown', trapTabKey);
  }

  mailTemplate = () => {
    const { packageVersion } = this.context;
    const { t, backendParameters } = this.context;
    let template = `${lb}${lb}${lb}${lb}----- ${t('Please_don’t_change_the_text_below._Type_your_message_above_this_line.')} -----
      ${lb}Connector type: ${backend}
      ${lb}Connector version: ${packageVersion}`;
    Object.keys(backendParameters).forEach((key) => {
      template += `${lb}${key}: ${backendParameters[key]}`;
    });
    template += `${lb}UserAgent: ${navigator.userAgent}`;
    return template;
  }

  render() {
    const { t, api, packageVersion, mode } = this.context;
    const { visible } = this.state;
    const { envUrl, userInfo } = api;
    const { fullName, initials } = userInfo;
    const mailSubject = `[${i18n.language}] MicroStrategy for ${backend} feedback`;

    const tooltipContent = (
      <div className="tooltip-content-container">
        <div className="user-info">
          <div className="circle"><span className="initials">{initials}</span></div>
          <div className="name-url">
            <h3>{fullName}</h3>
            <p>{envUrl}</p>
          </div>
        </div>
        <div className="links" tabIndex="-1">
          <ul>
            <li><button type="button" onClick={() => this.handleManageApplications()} disabled={mode === modes.PROJECT_SELECTION}>{t('Manage_Applications')}</button></li>
            <li><a href={helpUrl} target="_blank" rel="noopener noreferrer">{t('Help')}</a></li>
            <li><a href={`mailto:${CONTACT_US_MAIL}?subject=${mailSubject}&body=${this.mailTemplate()}`}>{t('Contact_Us')}</a></li>
            <li><button type="button" onClick={() => this.handleLogout()}>{t('Log_out')}</button></li>
          </ul>
          <div className="version-number">{t('Version_{{versionNumber}}', { versionNumber: packageVersion })}</div>
        </div>
      </div>
    );

    return (
      <div className="user-tooltip">
        <Popover content={tooltipContent} placement="leftTop" trigger="click" overlayClassName="user-tooltip-overlay" visible={visible}>
          <Tooltip title={t('More_items')} placement="left" mouseEnterDelay={1}>
            <button type="button" onClick={() => this.onOpenTootip()}>&#8942;</button>
          </Tooltip>
        </Popover>
      </div>
    );
  }
}

UserTooltip.contextType = StateContext;
