import React from 'react';
import PropTypes from 'prop-types';
import { Tooltip, Button } from 'antd';
import { LoadingText } from '@mstr/mstr-react-library';

import MSTRIcon from 'components/common/icons/MSTRIcon';
import Checkbox from 'components/common/Checkbox';
import Popover from 'components/common/Popover';
import DragAndDrop from 'components/common/DragAndDrop';
import CategoryBox from './CategoryBox';
import TextToInput from './TextToInput';

import './DetailsBox.scss';

// eslint-disable-next-line max-len
export default function DetailsBox({ dfName, objects = [], searchText = '', hideUnselected = false, isLoading = false, isPreviewed = false, isPreviewedByError = false, nameIsIncorrect = false, warningItems = [], onPreviewDf = () => {}, dfNameValidator = () => {}, objNameValidator = () => {}, dfNameSetter = () => {}, objNameSetter = () => {}, onObjsMove = () => {}, onObjsSelect = () => {}, t = (text) => text }) {
  const [displayIsPreviewedByError, setDisplayIsPreviewedByError] = React.useState(false);
  const boxRef = React.useRef(null);

  React.useEffect(() => {
    setDisplayIsPreviewedByError(!!isPreviewedByError);
  }, [isPreviewedByError]);

  const handleEditingErrorDfName = () => { setDisplayIsPreviewedByError(false); };

  let attributes = [];
  let metrics = [];
  const icons = {
    attribute: <MSTRIcon type="attribute" />,
    metric: <MSTRIcon type="metric" />,
  };

  const handleClickOnBox = React.useCallback(() => {
    onPreviewDf(dfName);
  }, [dfName, onPreviewDf]);

  React.useEffect(() => {
    const box = boxRef.current;
    !isPreviewed && box.addEventListener('click', handleClickOnBox);
    return () => {
      box.removeEventListener('click', handleClickOnBox);
    };
  }, [handleClickOnBox, isPreviewed]);

  !isLoading && objects
    .filter(({ name }) => !searchText || name.toLowerCase().includes(searchText))
    .forEach((obj) => {
      if (obj.type === 'attribute') {
        attributes.push(obj);
      } else { // if obj.type === 'metric'
        metrics.push(obj);
      }
    });

  if (hideUnselected) {
    metrics = metrics.filter(({ isSelected }) => isSelected);
    attributes = attributes.filter(({ isSelected }) => isSelected);
  }

  const selectAllSelected = metrics.length + attributes.length > 0 ? hideUnselected || !objects.some(({ isSelected }) => !isSelected) : false;

  const selectAllHandler = () => {
    const allObjsToApplyOn = objects.filter(({ isSelected }) => isSelected === selectAllSelected).map(({ name }) => name);
    onObjsSelect(dfName, allObjsToApplyOn);
  };

  const moveObjectsHandler = (target) => {
    const source = target === 'attributes' ? metrics : attributes;
    const allObjsToApplyOn = source.filter(({ isSelected }) => isSelected).map(({ name }) => name);
    onObjsMove(dfName, allObjsToApplyOn, target);
  };

  const dragAndDropHandler = (items, _target, index = -1) => {
    let target = _target.split('_');
    target = target[target.length - 1];
    const comparer = target === 'attributes'
      ? metrics.map(({ name }) => name)
      : attributes.map(({ name }) => name);
    onObjsMove(dfName, items.filter((item) => comparer.includes(item)), target, index);
  };

  const getRedInfoDisplay = (popoverContent) => (
    <Popover content={popoverContent} placement="top" trigger="hover">
      <span>
        <MSTRIcon type="red-info" />
      </span>
    </Popover>
  );

  React.useEffect(() => {
    !objects.filter(({ isSelected }) => isSelected).length && selectAllHandler();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isLoading]);

  if (isLoading) {
    return (
      <div
        className={`details-box ${isPreviewed ? 'previewed' : ''}`}
        ref={boxRef}
        data-dnd-item="data-frames-item"
      >
        <div className="box-title-container" title={dfName}>
          <Checkbox
            disabled
          />
          <span><TextToInput value={dfName} disabled /></span>
        </div>
        <div className="categorybox-holder loading-holder">
          <LoadingText text={t('Loading_Data')} />
        </div>
      </div>
    );
  }

  return (
    <div
      className={`details-box ${isPreviewed ? 'previewed' : ''}`}
      ref={boxRef}
      data-dnd-item="data-frames-item"
    >
      <div className="box-title-container" title={dfName}>
        <Checkbox
          checked={selectAllSelected}
          id={`${dfName}-select-all-absolute`}
          onChange={() => { selectAllHandler(); }}
        />
        <span>
          <TextToInput
            value={dfName}
            validator={dfNameValidator}
            setter={dfNameSetter}
            onToggle={handleEditingErrorDfName}
          />
          {nameIsIncorrect && getRedInfoDisplay(nameIsIncorrect.description)}
          {displayIsPreviewedByError && getRedInfoDisplay(isPreviewedByError.description)}
        </span>
      </div>
      <DragAndDrop
        containersGroupName={`${dfName}_dnd_container`}
        itemsGroupName={`${dfName}_dnd_item`}
        onDrop={dragAndDropHandler}
      >
        <CategoryBox
          title="Attributes"
          dfName={dfName}
          objects={attributes}
          warningItems={warningItems}
          icon={icons.attribute}
          nameValidator={objNameValidator}
          objNameSetter={objNameSetter}
          onObjsSelect={onObjsSelect}
          t={t}
        />
        <div className="arrow-holder">
          <Tooltip placement="left" trigger="hover" mouseEnterDelay={0.2} title={t('Convert_selected_Attributes_to_Metrics')}>
            <Button id={`${dfName}-button-attributes-to-metrics`} onClick={() => { moveObjectsHandler('metrics'); }}>&rsaquo;</Button>
          </Tooltip>
          <Tooltip placement="right" trigger="hover" mouseEnterDelay={0.2} title={t('Convert_selected_Metrics_to_Attributes')}>
            <Button id={`${dfName}-button-metrics-to-attributes`} onClick={() => { moveObjectsHandler('attributes'); }}>&lsaquo;</Button>
          </Tooltip>
        </div>
        <CategoryBox
          title="Metrics"
          dfName={dfName}
          objects={metrics}
          warningItems={warningItems}
          icon={icons.metric}
          nameValidator={objNameValidator}
          objNameSetter={objNameSetter}
          onObjsSelect={onObjsSelect}
          t={t}
        />
      </DragAndDrop>
    </div>
  );
}

DetailsBox.propTypes = {
  dfName: PropTypes.string.isRequired,
  objects: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    type: PropTypes.string,
    isSelected: PropTypes.bool,
  })),
  searchText: PropTypes.string,
  hideUnselected: PropTypes.bool,
  isLoading: PropTypes.bool,
  isPreviewed: PropTypes.bool,
  isPreviewedByError: PropTypes.shape({
    name: PropTypes.string,
    description: PropTypes.string,
  }),
  nameIsIncorrect: PropTypes.shape({
    name: PropTypes.string,
    description: PropTypes.string,
  }),
  warningItems: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    description: PropTypes.string,
    checked: PropTypes.bool,
  })),
  onPreviewDf: PropTypes.func,
  objNameValidator: PropTypes.func,
  dfNameValidator: PropTypes.func,
  dfNameSetter: PropTypes.func,
  objNameSetter: PropTypes.func,
  onObjsMove: PropTypes.func,
  onObjsSelect: PropTypes.func,
  t: PropTypes.func,
};
