/* eslint-disable react/sort-comp */
import React, { Component } from 'react';
import { displayModal } from 'components/common/Modals';

import { sortObjectsByProperty } from 'utilities/Utilities';
import StateContext, { modes } from 'StateContext';

import './BackendManager.scss';
import { Trans } from 'react-i18next';
import DetailsContainer from './DetailsContainer';

export default class BackendManager extends Component {
  constructor(props) {
    super(props);
    this.state = {
      datasetName: '',
    };
  }

  addEnvToSuggestions = (envInfo) => {
    const { updateStateContext } = this.context;
    const all = JSON.parse(envInfo);
    const temp = all;
    updateStateContext({ suggestedEnvironments: { all, temp } });
  }

  addRecentProjects = (recentProjectsStr) => {
    const { updateStateContext } = this.context;
    const recentProjects = JSON.parse(recentProjectsStr);
    updateStateContext({ recentProjects });
  }

  displayErrorMessage(type, details = null) {
    const { t, updateStateContext } = this.context;
    let title;
    switch (type) {
      case 'RconnectToEnvError':
        title = t('Error_when_connecting_to_the_environment._See_RStudio_console_for_details.');
        break;
      case 'RfetchError':
        title = t('Error_when_fetching_the_dataset._See_RStudio_console_for_details.');
        break;
      case 'RfileError':
        title = t("There_was_a_problem_saving_a_file_with_user's_preferences._See_RStudio_console_for_details.");
        break;
      case 'RfolderError':
        title = t("There_was_a_problem_creating_the_user's_settings_directory._See_RStudio_console_for_details.");
        break;
      case 'RexportError':
        title = t('Error_when_exporting_the_dataset._See_RStudio_console_for_details.');
        break;
      case 'RexportUnexpectedObjectTypeError':
        title = t("Error_when_exporting_the_dataset._You_can't_save_objects_to_this_folder.");
        break;
      case 'RcubeNotPublishedError':
        title = t('Error_when_fetching_dataset._The_cube_is_not_published.');
        break;
      case 'RemptyDatasetError':
        title = t('The_dataset_you_were_trying_to_fetch_contains_no_data.');
        break;
      case 'RreportOverwriteError':
        title = t('Error_when_exporting_the_dataset._Cannot_overwrite_a_report.');
        break;
      default:
        title = t('Error_occurred._See_RStudio_console_for_details.');
    }
    const content = details ? <DetailsContainer t={t}>{details}</DetailsContainer> : null;
    window.modalManager.hideModal();
    document.querySelector('.left-panel').focus();
    displayModal('error', title, content);
    updateStateContext({ importOrExportInProgress: false });
  }

  displayFetchStartMessage(datasetName) {
    const { updateStateContext, t } = this.context;
    const modalContent = (
      <p className="process-title">
        <Trans i18nKey="Importing_<1>{{datasetName}}</1>" t={t}>
            Importing <strong>{{ datasetName }}</strong>
        </Trans>
      </p>
    );
    window.modalManager.showModal('loading', modalContent, 'import-export-modal');
    this.setState({ datasetName });
    updateStateContext({ importOrExportInProgress: true });
  }

  displayFetchLoadingMessage(offset, total) {
    const { datasetName } = this.state;
    const { t } = this.context;
    const percent = `${((Number(offset) * 100) / (Number(total))).toFixed(0)}%`;
    const modalText = `${t('Rows_fetched')}: ${offset}/${total} (${percent})`;
    const modalContent = (
      <>
        <p className="process-title">
          <Trans i18nKey="Importing_<1>{{datasetName}}</1>" t={t}>
            Fetching <strong>{{ datasetName }}</strong>
          </Trans>
        </p>
        <p className="progress-info">
          {modalText}
        </p>
      </>
    );
    window.modalManager.showModal('loading', modalContent, 'import-export-modal');
  }

  // eslint-disable-next-line no-unused-vars
  displayFetchSuccessMessage = () => {
    const { datasetName } = this.state;
    const { t, updateStateContext } = this.context;
    const modalContent = (
      <Trans i18nKey="<0>{{datasetName}}</0>_imported_successfully" t={t}>
        <strong>{{ datasetName }}</strong> imported successfully
      </Trans>
    );
    window.modalManager.hideModal();
    displayModal('success', modalContent);
    updateStateContext({ mode: modes.IMPORT.MAIN, reloadDataSource: false, importOrExportInProgress: false });
    document.querySelector('.right-panel').focus();
  }

  displayExportStartMessage = (datasetName) => {
    const { updateStateContext, t } = this.context;
    const modalContent = (
      <p className="process-title">
        <Trans i18nKey="Exporting_<1>{{datasetName}}</1>" t={t}>
          Exporting <strong>{{ datasetName }}</strong>
        </Trans>
      </p>
    );
    window.modalManager.showModal('loading', modalContent, 'import-export-modal');
    this.setState({ datasetName });
    updateStateContext({ importOrExportInProgress: true });
  }

  // eslint-disable-next-line no-unused-vars
  displayExportSuccessMessage = () => {
    const { datasetName } = this.state;
    const { t, updateStateContext } = this.context;
    const modalContent = (
      <Trans i18nKey="<0>{{datasetName}}</0>_exported_successfully" t={t}>
        <strong>{{ datasetName }}</strong> exported successfully
      </Trans>
    );
    window.modalManager.hideModal();
    displayModal('success', modalContent);
    updateStateContext({ importOrExportInProgress: false });
    document.querySelector('.left-panel').focus();
  }

  displayUpdateLoadingMessage(datasetName) {
    const { t } = this.context;
    const modalContent = (
      <p className="process-title">
        <Trans i18nKey="Updating_<1>{{datasetName}}</1>" t={t}>
          Updating <strong>{{ datasetName }}</strong>
        </Trans>
      </p>
    );
    window.modalManager.showModal('loading', modalContent, 'import-export-modal');
  }

  displayPublishLoadingMessage(datasetName) {
    const { t } = this.context;
    const modalContent = (
      <p className="process-title">
        <Trans i18nKey="Publishing_<1>{{datasetName}}</1>" t={t}>
          Publishing <strong>{{ datasetName }}</strong>
        </Trans>
      </p>
    );
    window.modalManager.showModal('loading', modalContent, 'import-export-modal');
  }

  finishCubeUpdate = (success, cubeName) => {
    const { updateStateContext, t } = this.context;
    window.modalManager.hideModal();
    if (success) {
      const title = (
        <Trans i18nKey="<0>{{cubeName}}</0>_updated_and_published_successfully" t={t}>
          <strong>{{ cubeName }}</strong> updated and published successfully
        </Trans>
      );
      displayModal('success', title, '');
    } else {
      const title = (
        <Trans i18nKey="Updating_<1>{{cubeName}}</1>_failed" t={t}>
          Updating <strong>{{ cubeName }}</strong> failed
        </Trans>
      );
      const content = t('See_the_console_for_more_details.');
      displayModal('warning', title, content);
    }
    updateStateContext({ importOrExportInProgress: false, mode: modes.EXPORT.MAIN });
  }

  updateDataFramesList = (dataframesProps) => {
    const { updateStateContext } = this.context;
    const dataframes = {};
    const parsed = JSON.parse(dataframesProps);
    const sortedByName = sortObjectsByProperty(parsed, 'name');
    sortedByName.forEach(({ name }) => {
      dataframes[name] = {
        originalName: name,
      };
    });
    updateStateContext({ dataframes });
  }

  updateDataFrameContent = (dataframeContentObj, dataframeName, _fromJSON) => {
    const { updateStateContext } = this.context;
    const fromJSON = !!_fromJSON;
    const { content, types, originalName } = (fromJSON ? JSON.parse(dataframeContentObj) : dataframeContentObj);
    const dataframes = {
      ...this.context.dataframes,
      [dataframeName]: {
        content,
        types,
        originalName,
      },
    };
    updateStateContext({ dataframes, loadingDfContent: false });
  }

  setBackendEnvName = (backendEnvName) => {
    const { updateStateContext } = this.context;
    updateStateContext({ backendEnvName });
  }

  updateBackendParameters = (params) => {
    const { updateStateContext } = this.context;
    const backendParameters = JSON.parse(params);
    updateStateContext({ backendParameters });
  }

  automaticLogin = async (credentials, token, mode, selectedProjects) => {
    if (mode === modes.AUTHENTICATION) return;
    const { api, updateStateContext } = this.context;
    const { envName } = credentials;
    const { tokenChecker, keepSessionAlive } = this.props;
    api.setCredentials(credentials);
    api.token = token;
    const isSessionAlive = await api.isSessionAlive();
    let newMode;
    if (isSessionAlive) {
      await api.getSessionInfo();
      tokenChecker();
      keepSessionAlive();
      if (mode === modes.IMPORT.PREPARE_DATA) {
        newMode = modes.IMPORT.MAIN;
      }
      else if (mode === modes.EXPORT_DIR_SELECT || mode === modes.EXPORT_DATA_WRANGLER) {
        newMode = modes.EXPORT.MAIN;
      }
      else if (!mode) {
        newMode = modes.AUTHENTICATION;
      }
      else {
        newMode = mode;
      }
    }
    else {
      newMode = modes.AUTHENTICATION;
    }
    updateStateContext({ mode: newMode, selectedProjects, envName });
  }

  toggleImportOrExportInProgress = (state) => {
    const { updateStateContext } = this.context;
    updateStateContext({ importOrExportInProgress: state });
  }

  showAuthenticationPage = () => {
    const { updateStateContext } = this.context;
    updateStateContext({ mode: modes.AUTHENTICATION });
  }

  updatePackageVersionNumber = (packageVersion) => {
    const { updateStateContext } = this.context;
    updateStateContext({ packageVersion });
  }

  finishDataModeling = (success) => {
    const { updateStateContext, t, mode } = this.context;
    if (success) {
      const newMode = mode === modes.EXPORT.UPDATE.DATA_MODELING
        ? modes.EXPORT.UPDATE.POLICY_SELECTION
        : modes.EXPORT.CREATE.DIRECTORY_SELECTION;
      updateStateContext({ mode: newMode });
    } else {
      const title = t('Data_Modeling_process_failed.');
      const content = t('See_the_console_for_more_details.');
      displayModal('warning', title, content);
    }
    updateStateContext({ dataModelingInProgress: false });
  }

  render() {
    return null;
  }
}

BackendManager.contextType = StateContext;
