/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React from 'react';
import PropTypes from 'prop-types';
import { Checkbox } from 'antd';

import './DataframesSelection.scss';

export default function DataframesSelection({ dfNames, selectedDfs = [], previewedDf = null, onPreviewDf = () => {}, onSelect = () => {}, t = (text) => text }) {
  const handleClickOnItem = (name) => selectedDfs.includes(name) && onPreviewDf(name);

  return (
    <div className="dataframes-selection-container">
      <h2>{t('Available_Dataframes')}</h2>
      <ul
        className="dataframes-selection-list"
        data-dnd-container="data-frames-container"
      >
        {
          dfNames.map(({ current, initial }) => {
            const checked = selectedDfs.includes(current);
            const checkboxId = `checkbox-${current}`;
            return (
              <li
                key={current}
                title={initial}
                className={(previewedDf === current ? 'previewed' : '')}
                onClick={() => { handleClickOnItem(current); !checked && onSelect(current); }}
                data-dnd-item="data-frames-item"
                data-dnd-value={current}
              >
                <Checkbox onChange={() => onSelect(current)} checked={checked} id={checkboxId} />
                <div>{initial}</div>
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}

DataframesSelection.propTypes = {
  dfNames: PropTypes.arrayOf(PropTypes.shape({
    current: PropTypes.string.isRequired,
    initial: PropTypes.string.isRequired,
  })).isRequired,
  selectedDfs: PropTypes.arrayOf(PropTypes.string),
  previewedDf: PropTypes.string,
  onPreviewDf: PropTypes.func,
  onSelect: PropTypes.func,
  t: PropTypes.func,
};
