import React from 'react';
import PropTypes from 'prop-types';
import { applyDomClassName, getEventPath } from 'utilities/Utilities';

import './DragAndDrop.scss';

// === IMPLEMENTATION INSTRUCTIONS IN MOCK dragAndDrop.js ===

export const dndConfigConstants = {
  // container config:
  verticalItems: 'vertical-items', // items in container are set up vertically

  // items config:
};

export default function DragAndDrop({ children, containersGroupName, itemsGroupName, onDrop = () => {}, onDropSwapOnly = null }) {
  // implementation error handling
  if (!children) throw new Error('DragAndDrop component do not have any children components / DOM elements!');

  const [draggedItems, setDraggedItems] = React.useState([]);
  const [declaredTarget, setDeclaredTarget] = React.useState('');
  const [targetIndex, setTargetIndex] = React.useState(-1);
  const [containerDraggedOver, setContainerDraggedOver] = React.useState('');

  const hasConfiguration = (object, configuration) => (
    object.dataset.dndConfig && object.dataset.dndConfig.includes(configuration)
  );

  const getDivider = React.useCallback(
    (isVertical = false) => {
      let divider = document.getElementById('dnd-divider');
      if (!divider) {
        divider = document.createElement('div');
        divider.setAttribute('id', 'dnd-divider');
        const arrow1 = document.createElement('span');
        divider.appendChild(arrow1);
        const arrow2 = document.createElement('span');
        divider.appendChild(arrow2);
        document.body.appendChild(divider);
        applyDomClassName([divider], 'dnd-divider');
        applyDomClassName([arrow1], ['arrow', isVertical ? 'down' : 'right']);
        applyDomClassName([arrow2], ['arrow', isVertical ? 'up' : 'left']);
      }
      applyDomClassName([divider], 'vertical', isVertical);
      applyDomClassName([divider], 'first', targetIndex === 0);
      applyDomClassName([divider], 'last', targetIndex < 0);
      return divider;
    }, [targetIndex],
  );

  const setDivider = React.useCallback(
    (container, beforeWhichItem = null) => {
      container.insertBefore(getDivider(hasConfiguration(container, dndConfigConstants.verticalItems)), beforeWhichItem);
    }, [getDivider],
  );

  const clearDivider = React.useCallback(
    () => {
      const parent = getDivider().parentNode;
      parent.removeChild(getDivider());
    }, [getDivider],
  );

  const refreshDOMElements = React.useCallback(
    () => {
      const setGhostImage = async (items, event) => {
        const container = document.createElement('div');
        await applyDomClassName([container], 'drag-and-drop-ghost');
        items.forEach((item) => {
          const ghostDOM = item.cloneNode(true);
          container.appendChild(ghostDOM);
        });
        document.body.appendChild(container);
        event.dataTransfer && event.dataTransfer.setDragImage(container, 0, 0);
      };

      const clearGhostImages = () => {
        [...document.querySelectorAll('.drag-and-drop-ghost')]
          .forEach((element) => { document.body.removeChild(element); });
      };

      const freshContainers = [...document.querySelectorAll(`*[data-dnd-container="${containersGroupName}"]`)];
      const freshItems = [];
      freshContainers.forEach((container) => {
        const containedItems = [...container.querySelectorAll(`*[data-dnd-item="${itemsGroupName}"]`)];
        containedItems.forEach((item, index) => {
          item.dataset.dndParent = container.dataset.dndTarget || '';
          item.dataset.dndIndex = index;
        });
        freshItems.push(...containedItems);
      });
      const multichoiceAllowed = freshItems.some((item) => item.getAttribute('data-dnd-checked'));

      const getCheckedItems = (referenceItem) => (
        multichoiceAllowed
          ? freshItems
            .filter((item) => item.dataset.dndChecked === 'true' || item === referenceItem)
            .map(({ dataset }) => dataset)
          : [referenceItem.dataset]
      );

      freshContainers.filter(({ dataset }) => dataset.dndTarget).forEach((container) => {
        if (!container.dataset.dndListening) {
          container.dataset.dndListening = true;
          container.addEventListener('dragover', (event) => {
            event.preventDefault();
          }, true);
          container.addEventListener('dragenter', (event) => {
            event.preventDefault();
            setContainerDraggedOver(container.dataset.dndTarget || '');
            setTargetIndex(-1);
          }, true);
          container.addEventListener('dragleave', (event) => {
            if (!event.relatedTarget) event.relatedTarget = event.target.parentNode; // for Unit Tests
            if (
              getEventPath(event.relatedTarget.parentNode).includes(container.parentNode)
              && !getEventPath(event.relatedTarget).includes(container.nextElementSibling)
              && !getEventPath(event.relatedTarget).includes(container.previousElementSibling)
            ) return;
            event.preventDefault();
            clearDivider();
            setContainerDraggedOver('');
          });
          container.addEventListener('drop', (event) => {
            event.preventDefault();
            setDeclaredTarget(container.dataset.dndTarget || '');
          }, true);
        }
      });

      freshItems.forEach((item) => {
        const allCheckedItems = getCheckedItems(item);
        let temporaryClickDefinition;

        if (!item.dataset.dndListening) {
          item.dataset.dndListening = true;
          if (item.dataset.dndValue) {
            !item.draggable && item.setAttribute('draggable', true);
            item.addEventListener('dragstart', (event) => {
              if (item.onclick) { // lock click event from firing
                temporaryClickDefinition = item.onclick;
                item.onclick = null;
              }
              event.dataTransfer && event.dataTransfer.setData('text/plain', item.dataset.dndValue || 'dummy_text'); // for browser compatibility
              const filteredDOMItems = freshItems.filter(({ dataset }) => allCheckedItems.includes(dataset));
              setGhostImage(filteredDOMItems, event);
              const containersToApplyRestyling = onDropSwapOnly
                ? freshContainers.filter(({ dataset }) => dataset.dndTarget)
                : freshContainers.filter(({ dataset }) => dataset.dndTarget && dataset.dndTarget !== item.dataset.dndParent);
              applyDomClassName(containersToApplyRestyling, 'dnd-drop-ready');
              applyDomClassName(filteredDOMItems, 'dnd-dragged-item');
              setDraggedItems(allCheckedItems);
            }, true);
            item.addEventListener('dragend', (event) => {
              if (temporaryClickDefinition) item.onclick = temporaryClickDefinition; // reinstate click event
              event.dataTransfer && event.dataTransfer.clearData(); // for browser compatibility
              clearDivider();
              setContainerDraggedOver('');
              clearGhostImages();
              applyDomClassName(freshContainers, 'dnd-drop-ready', false);
              applyDomClassName(freshItems, 'dnd-dragged-item', false);
            }, true);
          }
          item.addEventListener('dragenter', (event) => {
            event.preventDefault();
            const testFunc = (target) => target.dataset && target.dataset.dndItem === itemsGroupName && target.dataset.dndIndex;
            const domItem = getEventPath(event).find(testFunc);
            setTargetIndex(parseInt(domItem.dataset.dndIndex, 10));
          }, true);
        }
      });
    }, [containersGroupName, itemsGroupName, clearDivider, onDropSwapOnly],
  );

  React.useEffect(() => {
    if (draggedItems.length && containerDraggedOver) {
      const items = document.querySelectorAll(`*[data-dnd-target="${containerDraggedOver}"] *[data-dnd-item="${itemsGroupName}"]`);
      if (!items.length) return;
      const container = items[0].parentNode;
      const target = targetIndex >= 0 ? items[targetIndex] : null;
      if (onDropSwapOnly) {
        setDivider(container, target);
      }
    }
  }, [draggedItems, targetIndex, itemsGroupName, containerDraggedOver, setDivider, onDropSwapOnly]);

  React.useEffect(() => {
    refreshDOMElements();
  }, [refreshDOMElements, children, declaredTarget]);

  React.useEffect(() => {
    if (!draggedItems.length) {
      setDeclaredTarget('');
      setTargetIndex(-1);
      return;
    }
    if (draggedItems.length && declaredTarget) {
      let finalItems = draggedItems
        .filter((item) => item.dndParent !== declaredTarget)
        .map((item) => item.dndValue || '');
      if (finalItems.length) {
        onDrop(finalItems, declaredTarget, targetIndex);
      } else {
        finalItems = draggedItems
          .filter((item) => item.dndParent === declaredTarget && item.dndIndex !== targetIndex)
          .map((item) => item.dndValue || '');
        finalItems.length && onDropSwapOnly && onDropSwapOnly(finalItems, declaredTarget, targetIndex);
      }
      setDeclaredTarget('');
      setDraggedItems([]);
      setTargetIndex(-1);
    }
  }, [draggedItems, declaredTarget, onDrop, onDropSwapOnly, targetIndex]);

  React.useEffect(() => {
    const containers = document.querySelectorAll('.dnd-drop-targeted');
    applyDomClassName([...containers], 'dnd-drop-targeted', false)
      .then(async () => {
        if (containerDraggedOver) {
          const container = document.querySelector(`[data-dnd-target="${containerDraggedOver}"].dnd-drop-ready`);
          await applyDomClassName([container], 'dnd-drop-targeted');
        }
      });
  }, [containerDraggedOver]);

  return (
    <>
      {children}
    </>
  );
}

DragAndDrop.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]).isRequired,
  containersGroupName: PropTypes.string.isRequired,
  itemsGroupName: PropTypes.string.isRequired,
  onDrop: PropTypes.func,
  onDropSwapOnly: PropTypes.func,
};
