import React, { Component } from 'react';

import StateContext from 'StateContext';

import './BackendManager.scss';
import { LoadingModal } from 'components/common/Modals';

export default class ModalManager extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalVisible: false,
      modalType: '',
      modalContent: '',
      modalOverlayClassName: '',
    };
  }

  showModal = (modalType, modalContent, modalOverlayClassName) => {
    this.setState({ modalType, modalContent, modalOverlayClassName, modalVisible: true });
  }

  hideModal = () => {
    this.setState({ modalType: '', modalContent: '', modalOverlayClassName: '', modalVisible: false });
  }

  render() {
    const { t } = this.context;
    const { modalVisible, modalType, modalContent, modalOverlayClassName } = this.state;
    let modal;
    if (modalType === 'loading') {
      modal = <LoadingModal t={t} content={modalContent} overlayClassName={modalOverlayClassName} />;
    }
    if (modalVisible) {
      return modal;
    }
    return null;
  }
}

ModalManager.contextType = StateContext;
