import React, { Component } from 'react';
import { Button, Tooltip, message } from 'antd';
import { FolderBrowser } from 'components/object-table/folder-browser';

import backendHelper from 'utilities/BackendHelper';
import Footer from 'components/common/Footer';
import MainContainer from 'components/common/MainContainer';
import StateContext, { modes } from 'StateContext';


export default class ExportData extends Component {
  constructor() {
    super();

    this.state = {
      searchText: '',
      updateLoading: false,
    };
  }

  componentDidMount() {
    const { updateStateContext } = this.context;
    document.addEventListener('keydown', this.handleKeyDown);
    updateStateContext({ selectedCubeToUpdate: {} });
    backendHelper.onExportDataMode();
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown);
  }

  handleKeyDown = (e) => {
    const activatedElements = [...document.querySelectorAll('button, a, [tabindex="0"], input[type="checkbox"], input[type="text"]')];
    const hasDefaultEnterKeyAction = activatedElements.includes(e.target);
    if (e.keyCode === 13 && !hasDefaultEnterKeyAction) {
      document.querySelector('#prepare').click();
    }
  }

  handleBack = () => {
    const { updateStateContext } = this.context;
    updateStateContext({
      mode: modes.IMPORT.MAIN,
    });
  }

  onObjectChosen = async (objectId, projectId, subtype, name, projectName) => {
    const { updateStateContext } = this.context;
    const selectedCubeToUpdate = { projectId, projectName, objectId, subtype, name };
    updateStateContext({ selectedCubeToUpdate });
  }

  onSearchChange = (searchText) => {
    this.setState({ searchText });
  }

  handleCreate = () => {
    this.context.updateStateContext({ mode: modes.EXPORT.CREATE.DATA_MODELING });
  }

  handleUpdate = async () => {
    const { api, t, selectedCubeToUpdate: { objectId, projectId } } = this.context;
    this.setState({ updateLoading: true });
    const rawCubeDefinition = await api.getCubeDefinition(objectId, projectId);
    if (!rawCubeDefinition) {
      message.error(t('Could_not_load_cube_definition.'));
    }
    else {
      const definition = this.parseCubeDefinition(rawCubeDefinition);
      const selectedCubeToUpdate = {
        ...this.context.selectedCubeToUpdate,
        definition,
      };
      this.context.updateStateContext({ mode: modes.EXPORT.UPDATE.DATA_MODELING, selectedCubeToUpdate });
    }
    this.setState({ updateLoading: false });
  }

  parseCubeDefinition = (rawDefinition) => {
    const definition = {};
    const { availableObjects } = rawDefinition.result.definition;
    availableObjects.tables.forEach(({ name }) => {
      definition[name] = [];
    });
    availableObjects.columns.forEach(({ tableName, columnName }) => {
      definition[tableName].push(columnName);
    });
    return definition;
  }

  render() {
    const { api, t, selectedCubeToUpdate, selectedProjects, importOrExportInProgress } = this.context;
    const { searchText, updateLoading } = this.state;
    const session = {
      USE_PROXY: false,
      url: `${api.envUrl}api`,
      authToken: api.token,
    };

    const isSomethingSelected = Object.entries(selectedCubeToUpdate).length !== 0;
    // eslint-disable-next-line no-nested-ternary
    const updateButtonTooltipTitle = isSomethingSelected
      ? importOrExportInProgress
        ? t('This_button_is_disabled_because_another_dataset_is_being_currently_fetched_or_exported.')
        : null
      : t('This_button_is_disabled_because_you_didn\'t_select_any_cube_to_update');
    const createButtonTooltipTitle = importOrExportInProgress ? t('This_button_is_disabled_because_another_dataset_is_being_currently_fetched_or_exported.') : null;

    return (
      <MainContainer>
        <FolderBrowser
          api={api}
          session={session}
          onObjectChosen={this.onObjectChosen}
          projectIds={selectedProjects}
          onSearchChange={this.onSearchChange}
          searchText={searchText}
          handleBack={this.handleBack}
          t={t}
        />
        <Footer
          left={<Button id="back" onClick={this.handleBack}>{t('Back')}</Button>}
          right={(
            <div>
              <Tooltip placement="top" title={updateButtonTooltipTitle} mouseEnterDelay={1} overlayClassName="info-tooltip">
                <Button id="update" onClick={this.handleUpdate} disabled={!isSomethingSelected || importOrExportInProgress} loading={updateLoading}>{t('Update')}</Button>
              </Tooltip>
              <Tooltip placement="topRight" title={createButtonTooltipTitle} mouseEnterDelay={1} overlayClassName="info-tooltip">
                <Button id="create" type="primary" onClick={this.handleCreate} disabled={importOrExportInProgress}>{t('Create')}</Button>
              </Tooltip>
            </div>
          )}
        />
      </MainContainer>
    );
  }
}

ExportData.contextType = StateContext;
