import React from 'react';
import MSTRIcon from 'components/common/icons/MSTRIcon';
import { modes } from 'StateContext';

export default function EmptyTablePlaceholder({ t, mode, userHasOwnCubes }) {
  if (mode === modes.EXPORT.MAIN && !userHasOwnCubes) {
    return (
      <div className="no-data-placeholder">
        <MSTRIcon type="create-cube" />
        <p>{t("The_list_is_empty._You_haven't_created_any_cube_yet.")}</p>
      </div>
    );
  }

  return (
    <div className="no-data-placeholder">
      <MSTRIcon type="empty-folder-no-results" />
      <p>{t('None_of_the_objects_matched_your_search.')}</p>
      <p>{t('Try_another_search.')}</p>
    </div>
  );
}
