import React from 'react';
import PropTypes from 'prop-types';
import { Select } from 'antd';

import './Select.scss';

const { Option } = Select;

export default function MstrSelect({ options, defaultValue = undefined, placeholder = undefined, onChange = () => {} }) {
  return (
    <Select defaultValue={defaultValue} onChange={onChange} placeholder={placeholder}>
      { options.map(({ value, displayValue }) => (
        <Option value={value} key={value}>{displayValue}</Option>
      ))}
    </Select>
  );
}

MstrSelect.propTypes = {
  options: PropTypes.arrayOf(PropTypes.shape({
    value: PropTypes.string,
    displayValue: PropTypes.string,
  })).isRequired,
  defaultValue: PropTypes.string,
  placeholder: PropTypes.string,
  onChange: PropTypes.func,
};
