import React from 'react';
import { Input } from 'antd';

import './SearchBar.scss';
import { modes } from 'StateContext';
import StepIndicator from 'components/export-data/StepIndicator';

export default function SearchBar({ t, mode, onSearch, searchText, stepIndicatorVisible = false }) {
  let title = '';
  switch (mode) {
    case modes.IMPORT.MAIN:
      title = 'Object_List';
      break;
    case modes.EXPORT.MAIN:
      title = 'Cube_List';
      break;
    case modes.PROJECT_SELECTION:
      title = 'Select_Applications';
      break;
    default:
      title = '';
  }

  return (
    <div className="search-bar">
      {stepIndicatorVisible ? <StepIndicator mode={mode} t={t} /> : <h2>{t(title)}</h2> }
      <div className="object-search">
        <Input.Search
          placeholder={`${t('Search')}...`}
          onChange={onSearch}
          className="project-chooser-search"
          value={searchText}
          maxLength={255}
        />
      </div>
    </div>
  );
}
