const objectTypesEnum = [
    {
        type: 8,
        subtype: 2048,
        name: 'Folder',
    },
    {
        type: 3,
        subtype: 768,
        name: 'Report',
    },
    {
        type: 3,
        subtype: 769,
        name: 'Report',
    },
    {
        type: 3,
        subtype: 774,
        name: 'Report',
    },
    {
        type: 55,
        subtype: 14081,
        name: 'Document',
    },
    {
        type: 3,
        subtype: 776,
        name: 'Cube',
    },
    {
        type: 3,
        subtype: 779,
        name: 'Cube',
    }
];

const getTypeDescription = (value, subValue) => {
    const foundType = objectTypesEnum.find((type) => {
        return type.type === value
            && type.subtype === subValue;
    });
    if (!foundType) {
        return;
    }
    return foundType.name;
};

const getTypeValues = (name) => {
    const foundType = objectTypesEnum.find((type) => {
        return type.name === name;
    });
    if (!foundType) {
        return;
    }
    return {
        type: foundType.type,
        subtype: foundType.subtype,
    };
};

export const objectTypes = {objectTypesEnum, getTypeDescription, getTypeValues};