import React from 'react';
import { Collapse } from 'antd';

import './DetailsContainer.scss';

export default function DetailsContainer({ children, t = (text) => text }) {
  const [collapsed, setCollapsed] = React.useState(true);

  return (
    <div className="details-container">
      <Collapse onChange={() => setCollapsed((state) => !state)} bordered={false}>
        <Collapse.Panel header={collapsed ? t('Show_details') : t('Hide_details')}>
          <div className="details-content">
            {children}
          </div>
        </Collapse.Panel>
      </Collapse>
    </div>
  );
}
