import React, { Component } from 'react';

import { Modal, Button } from 'antd';
import './object-browser.css';
import { LoadingText } from '@mstr/mstr-react-library';
import { supportedTypes, disableFocusOnMouseClick, sleep } from 'utilities/Utilities';
import StateContext from 'StateContext';

export default class DatasetInfomodal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalVisible: false,
      loadingProperties: true,
      datasetProps: {
        name: '',
        type: '',
        location: '',
        description: '',
        dateCreated: '',
        dateModified: '',
        owner: '',
        link: '',
        size: '',
      },
    };
  }

  componentDidMount() {
    disableFocusOnMouseClick('button');
  }

  modalContent = () => {
    const { t } = this.context;
    const { name, type, location, description, dateCreated, dateModified, owner, size } = this.state.datasetProps;
    const { datasetId } = this.props;
    const { loadingProperties } = this.state;
    if (loadingProperties) return <LoadingText text={t('Loading_Data')} />;
    return (
      <table>
        <tbody>
          <tr>
            <td>{t('Name')}</td>
            <td><div>{name}</div></td>
          </tr>
          <tr>
            <td>{t('Type')}</td>
            <td><div>{t(type)}</div></td>
          </tr>
          <tr>
            <td>{t('Location')}</td>
            <td><div>{location}</div></td>
          </tr>
          <tr>
            <td>{t('ID')}</td>
            <td><div>{datasetId}</div></td>
          </tr>
          <tr>
            <td>{t('Description')}</td>
            <td><div>{description}</div></td>
          </tr>
          <tr>
            <td>{t('Created')}</td>
            <td>{t('refreshed_date', { date: new Date(dateCreated) })}</td>
          </tr>
          <tr>
            <td>{t('Modified')}</td>
            <td>{t('refreshed_date', { date: new Date(dateModified) })}</td>
          </tr>
          <tr>
            <td>{t('Owner')}</td>
            <td><div>{owner}</div></td>
          </tr>
          {
            size > 0
            && (
              <tr>
                <td>{t('Size')}</td>
                <td>
                  {(size / 10 ** 6).toFixed(1)}
                  MB
                </td>
              </tr>
            )
          }
        </tbody>
      </table>
    );
  }

    showModal = async () => {
      this.setState({ modalVisible: true, loadingProperties: true });
      const { datasetId, projectId } = this.props;
      const { api } = this.context;
      const { envUrl, token } = api;
      const { name, owner, subtype, dateCreated, dateModified, ancestors, description } = await api.getObjectParams(envUrl, token, projectId, datasetId);
      const type = supportedTypes.reportTypes.includes(subtype) ? 'Report' : 'Dataset';
      const size = type === 'Dataset' ? await api.getCubeSize(envUrl, token, projectId, datasetId) : undefined;
      const location = ancestors.map((e) => e.name).join(' > ');
      const datasetProps = { name, type, location, datasetId, description, dateCreated, dateModified, owner: owner.name, link: '', size };
      this.setState({ datasetProps, loadingProperties: false });
      await sleep(1);
      disableFocusOnMouseClick('button');
    }

    handleOk = () => {
      this.setState({ modalVisible: false });
    }

    render() {
      const { x, y, contextMenuVisible } = this.props;
      const { t } = this.context;
      return (
        <div>
          <Modal
            title={t('Properties')}
            visible={this.state.modalVisible}
            wrapClassName="dataset-props-popover"
            zIndex="1500"
            onCancel={this.handleOk}
            footer={<Button type="primary" onClick={this.handleOk}>{t('OK')}</Button>}
          >
            {this.modalContent()}
          </Modal>
          {contextMenuVisible && (
          <div className="properties-context-menu" style={{ left: `${x - 230}px`, top: `${y - 100}px` }}>
            <button type="button" onClick={() => this.showModal()}>{t('Properties')}</button>
          </div>
          )}
        </div>
      );
    }
}

DatasetInfomodal.contextType = StateContext;
