import React, { Component } from 'react';
import { Button, message, Tooltip } from 'antd';
import { FolderBrowser } from 'components/object-table/folder-browser';

import backendHelper, { backend } from 'utilities/BackendHelper';
import Footer from 'components/common/Footer';
import MainContainer from 'components/common/MainContainer';
import { supportedTypes, disableFocusOnMouseClick } from 'utilities/Utilities';
import StateContext, { modes } from 'StateContext';


export default class ObjectBrowser extends Component {
  constructor() {
    super();

    this.state = {
      searchText: '',
      notImportable: true,
      isCubePublished: false,
      prepareDataLoading: false,
    };
  }

  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyDown);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown);
  }

    handleKeyDown = (e) => {
      const activatedElements = [...document.querySelectorAll('button, a, [tabindex="0"], input[type="checkbox"], input[type="text"]')];
      const hasDefaultEnterKeyAction = activatedElements.includes(e.target);
      if (e.keyCode === 13 && !hasDefaultEnterKeyAction) {
        document.querySelector('#prepare').click();
      }
    }

    handleBack = () => {
      const { updateStateContext } = this.context;
      updateStateContext({
        mode: modes.PROJECT_SELECTION,
        selectedDataset: {},
      });
    }

    handleImport = () => {
      const { api, selectedDataset } = this.context;
      backendHelper.sendDatasetInfo(api, selectedDataset);
    }

    handleGenerateCode = () => {
      const { selectedDataset: { name, projectName, objectId, subtype, parameters }, api, t } = this.context;
      backendHelper.generateImportCode(name, projectName, objectId, subtype, parameters, api, t);
    }

    onObjectChosen = async (objectId, projectId, subtype, name, projectName) => {
      const { updateStateContext } = this.context;
      const selectedDataset = { projectId, projectName, objectId, subtype, name, parameters: { length: 0 } };
      updateStateContext({ selectedDataset });
      this.setState({ notImportable: true });
      message.destroy();
      const isCube = supportedTypes.cubeTypes.includes(subtype);
      let notImportable = false;
      let isCubePublished = true;
      let warningMsg = isCube;
      if (isCube) {
        warningMsg = await this.checkIfCubeSupported(objectId, projectId);
        isCubePublished = !warningMsg;
      } else {
        warningMsg = await this.checkIfReportSupported(objectId, projectId);
      }
      if (warningMsg) {
        notImportable = true;
        message.warning(warningMsg);
      }
      this.setState({ notImportable, isCubePublished }, () => {
        disableFocusOnMouseClick('button');
      });
    }

    checkIfCubeSupported = async (cubeId, projectId) => {
      const { api, t } = this.context;
      const cubeStatus = await api.getCubeStatus(cubeId, projectId);
      if (cubeStatus === '0') {
        return t('This_cube_is_not_published.');
      }
      return '';
    }

    checkIfReportSupported = async (reportId, projectId) => {
      const { api, t } = this.context;
      const reportDefinition = await api.getReportDefinition(reportId, projectId);
      const { availableObjects: { customGroups, consolidations }, grid: { crossTab, metricsPosition } } = reportDefinition;
      if (customGroups.length) {
        return t('Reports_with_custom_groups_are_not_supported.');
      }
      if (consolidations.length) {
        return t('Reports_with_consolidations_are_not_supported.');
      }
      if (crossTab && metricsPosition.axis === 'rows') {
        return t('Cross-tabbed_reports_with_metrics_positioned_in_rows_are_not_supported.');
      }
      const isPrompted = await api.isPrompted(reportId, projectId);
      if (isPrompted === true) {
        return t('Prompted_reports_are_currently_not_supported.');
      }
      if (typeof isPrompted === 'number') {
        return t('This_report_is_not_supported.');
      }
      return '';
    }

    onPrepareData = async () => {
      const { updateStateContext, api } = this.context;
      this.setState({ prepareDataLoading: true });
      const isSessionAlive = await api.isSessionAlive();
      this.setState({ prepareDataLoading: false });
      if (isSessionAlive) {
        updateStateContext({ mode: modes.IMPORT.PREPARE_DATA });
      }
    }

    onSearchChange = (searchText) => {
      this.setState({ searchText });
    }

    render() {
      const { api, t, savedRecord, importOrExportInProgress, selectedDataset, selectedProjects } = this.context;
      const { prepareDataLoading, notImportable, isCubePublished, searchText } = this.state;
      const session = {
        USE_PROXY: false,
        url: `${api.envUrl}api`,
        authToken: api.token,
      };

      const isRStudio = backend === 'RStudio';
      const isSomethingSelected = Object.entries(selectedDataset).length !== 0;
      const importButtonDisabled = !selectedDataset.objectId || notImportable || importOrExportInProgress;
      // eslint-disable-next-line no-nested-ternary
      const disabledButtonTooltipInfo = importOrExportInProgress
        ? isRStudio
          ? null
          : t('This_button_is_disabled_because_another_dataset_is_being_currently_fetched_or_exported.')
        : isSomethingSelected
          ? isCubePublished
            ? t('This_button_is_currently_disabled_because_the_dataset_you_selected_is_not_supported')
            : t('This_button_is_currently_disabled_because_the_cube_you_selected_is_not_published.')
          : t("This_button_is_currently_disabled_because_you_didn't_select_any_data.");
      const importButtonTooltipTitle = importButtonDisabled ? disabledButtonTooltipInfo : null;
      const prepareButtonTooltipTitle = importButtonDisabled ? disabledButtonTooltipInfo : null;
      return (
        <MainContainer>
          <FolderBrowser
            api={api}
            session={session}
            onObjectChosen={this.onObjectChosen}
            projectIds={selectedProjects}
            onSearchChange={this.onSearchChange}
            searchText={searchText}
            handleBack={this.handleBack}
            t={t}
            chosenItem={savedRecord}
          />
          <Footer
            left={<Button id="back" onClick={this.handleBack}>{t('Back')}</Button>}
            right={(
              <div>
                { false && isRStudio
                && (
                <Tooltip placement="top" title={importButtonTooltipTitle} mouseEnterDelay={1} overlayClassName="info-tooltip">
                  <Button id="import" onClick={this.handleGenerateCode} disabled={importButtonDisabled}>{t('Generate_Code')}</Button>
                </Tooltip>
                )}
                <Tooltip placement="top" title={importButtonTooltipTitle} mouseEnterDelay={1} overlayClassName="info-tooltip">
                  <Button id="import" onClick={this.handleImport} disabled={importButtonDisabled}>{t('Import')}</Button>
                </Tooltip>
                <Tooltip placement="topRight" title={prepareButtonTooltipTitle} mouseEnterDelay={1} overlayClassName="info-tooltip">
                  <Button id="prepare" type="primary" disabled={importButtonDisabled || prepareDataLoading} onClick={this.onPrepareData}>{t('Prepare_Data')}</Button>
                </Tooltip>
              </div>
            )}
          />
        </MainContainer>
      );
    }
}

ObjectBrowser.contextType = StateContext;
